import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class AllRisksPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }



    setCatenaMoralRisk (catenaMoralRisk) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, catenaMoralRisk, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-catenaMoralRisk", JSON.stringify({
            catenaMoralRisk: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getCatenaMoralRisk () {
        return this.getTemporaryData("overdue-catenaMoralRisk");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getCatenaMoralRisk() ? this.getCatenaMoralRisk().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}