import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { ComplianceAnalysisPageCache } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/compliance-analysis/common/pageCache";
import { BranchExamApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/base/localData";

@Component({
  selector: 'compliance-analysis',
  templateUrl: './compliance-analysis.component.html',
  styleUrls: ['./compliance-analysis.component.scss']
})
export class ComplianceAnalysisComponent implements OnInit, OnChanges {

    @Input() inValue: any = [];
    @Input() isDetailFlag: Boolean;
    issuccess=false;
    complianceAnalyzeList: any[] = [];
    complianceAnalyze: any = {
        compliance: '',
        conditionExplain: ''
    };        // 合规性分析

    //3.合规性分析
    complianceOption: any[] = [{ label: '是', value: '1' }, { label: '否', value: '0' }];

    history: HistoryInfor = new HistoryInfor();

    statusKeeper: ComplianceAnalysisPageCache = new ComplianceAnalysisPageCache(this.commfunc);

    markFlag: string = '';
    localData: BranchExamApprovalData = new BranchExamApprovalData(this.commfunc);

    constructor(public commfunc: Commfunc) { }

    ngOnInit() {
        this.getConfidential();
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.grapComponentData();
    }

    // 抓取数据
    grapComponentData () {
        this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
        if (this.inValue && this.inValue.length > 0) {
            this.complianceAnalyzeList = this.inValue;
            let obj = {};
            this.history.historyControl(this.complianceAnalyzeList, obj);
            this.complianceAnalyze = obj;
        }
    }

    // 向外部传递数据
    inputCall (event) {
        this.setComplianceAnalyze();
    }

    dropdownChange (event) {
        this.setComplianceAnalyze();
    }


    setComplianceAnalyze () {
        if(this.complianceAnalyze.compliance&&this.complianceAnalyze.conditionExplain){
            this.complianceAnalyze['issucess']=true;
            this.issuccess=false;
          } else{
            this.complianceAnalyze['issucess']=false;
            this.issuccess=true;
          }
        this.statusKeeper.setComplianceAnalyze(this.complianceAnalyze);
    }

    getConfidential () {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }


}
