import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class CreditDevelopmentPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }



    setWorkCarryOutCondition (workCarryOutCondition) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, workCarryOutCondition, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-workCarryOutCondition", JSON.stringify({
            workCarryOutCondition: workCarryOutCondition,
            timeStamp: this.getTimeStamp().timeStamp
        }));
      }

    getWorkCarryOutCondition () {
        return this.getTemporaryData("overdue-workCarryOutCondition");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getWorkCarryOutCondition() ? this.getWorkCarryOutCondition().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}