import { Component, OnInit, Output, Input, OnChanges, OnDestroy } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { OverdueHttpService } from '../http/overdue.http.service';
import { OverdueCaseBean } from './bean/overdue-case.bean';
import { JoinGroupComponent } from '../../custom-marketing/group/join-group/join-group.component';
import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { UrlTool } from "app/pages/tzb/custom/overdue/common/url";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { OverdueCaseTab } from "app/pages/tzb/custom/overdue/overdue-case/base/tab";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";
import { DateHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { PageData } from "app/pages/tzb/custom/overdue/common/pageData";

import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

// import { ParentChildCommunicationService } from '../../service/parent-child-comm.service';
import { ParentChildCommunicationService } from '../../../custom/credit-ccm/common/service/parent-child-comm.service';



@Component({
    selector: "overdue-case",
    templateUrl: './overdue-case.component.html',
    styleUrls: ['./overdue-case.component.scss'],
    providers: [ConfirmationService, Commfunc,ParentChildCommunicationService]
})

export class OverdueCase implements OnInit, OnDestroy {

    ch: any = CALENDAR_CN;
    //任务推送时间
    latestTaskPushDate: string = '';
    custNo: string = "";
    contractNumber: string = "";
    overdueCaseAnalyzeId: string = "";
    // 页面是否是查看状态
    isDetailFlag: boolean;   // 从列表跳转是展示详情，从待办跳转是可操作的
    markFlag: string;       // 是否是 逾期代办 历史
    isMyOrder: boolean = false;
    // 任务ID
    taskId: string = '';
    processId: string = '';
    reminder:any;

    operatingLevel:any;//审批人层级

    overdueCaseTab: OverdueCaseTab = new OverdueCaseTab(this.commfunc, this.router);
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    pageStatus: PageStatusKeeper = new PageStatusKeeper(this.commfunc);
    localData: LocalData = new LocalData(this.commfunc);
    // urlTool: UrlTool = new UrlTool();
    dateHelper: DateHelper = new DateHelper();
    pageData: PageData = new PageData(this.commfunc);

    //基本信息
    baseInfo: any = {
        overdueCaseAnalyzeId: '',
        overdueCaseName: '',
        custNo: '',
        custName: '',
        contractNumber: '',
        productNo: '',
        productName: ''
    };

    urlParam: string;//路径

    // 流水号
    mainFlowNo: string = '';
    commonHeader: any;
    dataMap: any;
    isErrorOrEmpty: boolean = false;
    isNewToPage: boolean = false;
    isRefreshPage: boolean = false;

    // 模块化参数
    bookmarkList = [];
    viewId: any;
    index: number;//页签

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public route: ActivatedRoute,
        private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService,
    ) {
        console.log("lllll")
        this.localData.clearSessionData();
        // this.codeValues();
        this.pageStatus.generateTimeStamp();
        this.urlParam = window.location.host;
    }

    ngOnInit() {
        //点击下一步，其他tab页取消置灰
        // this.communicationService.changeTabview.subscribe(data => {
        //     this.index = data.index;
        //     this.bookmarkList.forEach((item, inx) => {
        //         item['disabledFlag'] = false;
        //     })
        // })
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        // 接收路由传参
        this.filteTabs();
        this.grapParameters();
        this.pageInit();
        // this.overdueModule();
    }

    ngOnChanges() {

    }

    grapParameters() {
        // let parameter = Object.keys(this.route.snapshot.params).length ? this.route.snapshot.params : this.getInitData();
        let parameter = null;
        if (Object.keys(this.route.snapshot.params).length) {
            parameter = this.route.snapshot.params;
        } else {
            // 页面刷新
            parameter = this.getInitData();
            this.isRefreshPage = true;
        }

        if (parameter && Object.keys(parameter).length != 0) {
            this.localData.setTemporaryData('overdue-initdata', parameter);

            // 公用
            if (parameter.from == 'reminder-do') {
                // 代办跳过来
                let param = this.localData.getTemporaryData('overdue-params-from-reminder-do');
                this.reminder = param;
                this.taskId = param.taskId;
                this.markFlag = param.markFlag;
                this.processId = param.processId;
                this.mainFlowNo = param.mainFlowNo;
                this.isDetailFlag = param.markFlag && param.markFlag == '2' ? true : false
                if (param.dataMap) {
                    let t = JSON.parse(param.dataMap)
                    
                    this.dataMap = t;
                    this.overdueCaseAnalyzeId = t.inspectAnalyzeTaskId || t.overdueCaseAnalyzeId;
                }
            } else if (parameter.param) {
                // 转化贷款
                let param = JSON.parse(parameter.param);
                this.isDetailFlag = true;
                this.custNo = param.custNo;
                this.contractNumber = param.contractNo;
            } else {
                // 查询列表 跳过来
                this.isDetailFlag = parameter.isDetailFlag == 'true' ? true : false;
                this.overdueCaseAnalyzeId = parameter.overdueCaseAnalyzeId;
                this.isMyOrder = true;
            }
            this.isNewToPage = true;
            this.showBaseData();
        }
    }

    pageInit() {
        // this.operatingLevel = this.code.OperatingLevel;
        this.overdueCaseTab.loadConfig({ isMyOrder: this.isMyOrder });
        this.overdueCaseTab.showTabsRouter();
        this.overdueCaseTab.filteTabs();
    }

    getInitData() {
        return this.localData.getTemporaryData('overdue-initdata');
    }

    //查询基本信息
    showBaseData() {
        let param = null;
        if (this.overdueCaseAnalyzeId) {
            param = {
                overdueCaseAnalyzeId: this.overdueCaseAnalyzeId,           //案例编号
                //( 1-客户经理分析详情  2-支行审批详情 3-分行审批详情 4-总行审批详情 x-案例基本信息)
                // queryType: "x"                     
            }
        } else if (this.custNo && this.contractNumber) {
            param = {
                custNo: this.custNo,
                contractNumber: this.contractNumber
                // queryType: "x"                     
            }
        }

        this.httpService.overdueCaseDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                debugger;
                //任务推送时间
                if (data.sysPushDate) {
                    this.latestTaskPushDate = this.dateHelper.getDate(data.sysPushDate);
                }
                // 基本信息
                this.baseInfo['overdueCaseAnalyzeId'] = data.overdueCaseAnalyzeId;     //案例编号
                // this.baseInfo['overdueCaseAnalyzeId'] = this.overdueCaseAnalyzeId;
                this.baseInfo['overdueCaseName'] = data.overdueCaseName;               //逾期案例名称
                this.baseInfo['custNo'] = data.custNo;                                 //客户号
                this.baseInfo['custName'] = data.custName;                             //客户名称
                this.baseInfo['contractNumber'] = data.contractNumber;                //合同编号/卡号
                this.baseInfo['productNo'] = data.productNo;                          //产品编号
                this.baseInfo['productName'] = data.productName;                      //产品名称

                // this.pageData.setData(data);
                // this.pageData.setBaseInfo(this.baseInfo);

                this.setBaseInfo();
                this.apply(0, this.overdueCaseTab.tabsRouterInfo[0]);
                this.overdueModule();
        
                // 默认跳转第一个tabs标签页
               
            } else {
                this.isErrorOrEmpty = true;
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.isErrorOrEmpty = true;
            this.commonlyMessage.logicError('调用失败!');
        });
    }


    // tabs标签页切换事件
    apply(index, item) {
        let param = {
            taskId: this.taskId,
            processId: this.processId,
            baseInfo: JSON.stringify(this.baseInfo)
        }
        this.localData.setTemporaryData("overdue-confidential", {
            isDetailFlag: this.isDetailFlag ? true : false,
            operatingLevel: this.overdueCaseTab.operatingLevel,
            markFlag: this.markFlag,
            mainFlowNo: this.mainFlowNo,
            dataMap: this.dataMap,
            backUrl: this.getBackNumber()
        });
        console.log("param")
        console.log(param)
        // this.overdueCaseTab.showTabs(index, item, param);
    }

    getBackNumber() {
        let t = this.localData.getOverdueConfidential();
        let number = -2;
        if (this.isNewToPage && !this.isRefreshPage) {
            this.isNewToPage = false;
            this.isRefreshPage = false;
        } else {
            number = t.backUrl && !isNaN(t.backUrl) ? parseInt(t.backUrl) : -2;
            number -= 1;
        }

        return number;
    }

    setBaseInfo() {
        this.localData.setTemporaryData("overdue-baseInfo", this.baseInfo);
    }


    ngOnDestroy(): void {

    }
    // 逾期案例模块化

    overdueModule() {
        let overdueData = {
            isModule:'1',
            taskId: this.taskId,
            processId: this.processId,
            baseInfo: JSON.stringify(this.baseInfo),
            allowed:this.operatingLevel
        }
        this.commfunc.setSessionDataCH('overdueData', JSON.stringify(overdueData));

        this.viewId = JSON.parse(this.commfunc.getSessionDataCH('viewId'));
        var viewConfigId = [];
        var viewName = [];
        this.viewId.forEach(e => {
            viewConfigId.push(e.a);
            viewName.push(e.b);
        });
        const param = {
            'viewConfigId': viewConfigId,
            'viewName': viewName
        };
        this.creditCcmService.queryAllSourcesForView(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                let arr = data.list;
                this.bookmarkList=[];
                    if (this.commonHeader.extParam.managePermission == 'N') {
                      if (this.reminder.workType !== 5 && this.reminder.workType !== 4 && this.reminder.workType !== 7) {
                        for (let i = 0; i < arr.length; i++) {
                          if (data.list[i].bookmarkId !== '1760004') {
                            this.bookmarkList.push(data.list[i]);
                          }
                        }
                      } else {
                        this.bookmarkList = data.list;
                      }
                    } else {
                        this.bookmarkList = data.list;
                      }
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
                //   this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });
    }
    // 模块化判断审批人层级
    filteTabs () {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let isCustomerMan = commonHeader.extParam.managePermission == 'N';
        let logLevel = 0;
        if (isCustomerMan) {
            // 其他
            logLevel = 2;
        } else {
            logLevel = this.loginLevel(commonHeader.extParam.operatingLevel);
        }
        this.operatingLevel = logLevel;
        // this.filter(logLevel);
    }
    loginLevel (type) {
        let ret: number = 0;  // 索引，对应 this.tabsRouterInfo 成员中的index
        switch (type) {
            // 支行
            case 'TEAM':
            case 'DISTRICT':
            case 'SUB_BRANCH':
            case 'DISTRICT_DT':
            case 'SUB_BRANCH_DT':
                ret = 3;
            break;
            // 分行
            case 'BRANCH':
            case 'BRANCH_DT':
                ret = 4;
            break;
            // 总行
            case 'GROUP':
            case 'GENERAL_BANK':
            case 'GENERAL_BANK_DT':
                ret = 5;
            break;
        }
        return ret;
    }

//    判断模块化层级
    // 页签切换事件
    handleChange(event) {
        this.index = event.index;
        // if (!this.transferCreditBean.isPreview) {
        //   if ((this.bookmarkList[0].bookmarkName == '转化方案' && event.index == 0) || !(this.tabelChangeArr.length > 0 && this.tabelChangeArr.indexOf(event.index) > -1)) {

        //     this.triggerChildComponentsQuery(this.bookmarkList[event.index], this.tabFlag);
        //     this.tabelChangeArr.push(event.index);
        //   }

        // }
    }

}