import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ClearSchemePageCache } from "app/pages/tzb/custom/overdue/overdue-case/branch-approval/clearance-plan/common/pageCache";
import { CreditInfoData } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/base/localData";
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";

@Component({
  selector: 'clearance-plan',
  templateUrl: './clearance-plan.component.html',
  styleUrls: ['./clearance-plan.component.scss']
})
export class ClearancePlanComponent implements OnInit, OnChanges {

      // 输入数据
      @Input() inValue: any = [];
      @Input() isDetailFlag: Boolean;
      clearSchemeList: any[] = [];
      clearScheme: any = {
          clearStep: '',
          clearEffect: ''
      }
      clearIdea: string = '';
  
      display: Boolean = false;
  
      history: HistoryInfor = new HistoryInfor();
      pageProcess: PageProcess = new PageProcess();
      statusKeeper: ClearSchemePageCache = new ClearSchemePageCache(this.commfunc);
      localData: CreditInfoData = new CreditInfoData(this.commfunc);
      markFlag: string = '';
  
      // 4.授信工作开展情况
      constructor(public commfunc: Commfunc) { 
      }
  
      ngOnInit() {
          this.pageProcess.setInit();
          this.getConfidential();
          console.log(this.history.historyOptions);
          console.log(this.markFlag);
          console.log(this.isDetailFlag);
      }
  
      ngOnChanges(changes: SimpleChanges): void {
            if (this.pageProcess.getInit()) {
                this.grapComponentData();
            }
      }
  
  
      // 抓取数据
      grapComponentData () {
          this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
          if (this.inValue && this.inValue.clearSchemeList) {
              this.clearIdea = this.inValue.clearIdea;
              this.clearSchemeList = this.inValue.clearSchemeList;
              let obj = {};
              this.history.historyControl(this.clearSchemeList, obj);
              this.clearScheme = obj;
          }
      }
  
  
      change (event) {
          this.history.historyOptionsChange(event);
      }
  
      inputCall (event) {
          this.setClearScheme();
      }
  
      setClearScheme () {
          this.statusKeeper.setClearScheme(this.clearScheme);
      }

      getConfidential () {
          let confidential = this.localData.getOverdueConfidential();
          if (confidential) {
              this.markFlag = confidential.markFlag;
          }
      }
}
