import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class JobEvalIntroPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }


    setWorkEvaluteResume (workEvaluteResume) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, workEvaluteResume, 'historyProcess');
          this.commfunc.setSessionDataCH("overdue-workEvaluteResume", JSON.stringify({
            workEvaluteResume: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getWorkEvaluteResume () {
        return this.getTemporaryData("overdue-workEvaluteResume");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getWorkEvaluteResume() ? this.getWorkEvaluteResume().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}