import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { JobEvalIntroPageCache } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/job-eval-intro/common/pageCache";
import { BranchExamApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/base/localData";

@Component({
  selector: 'job-eval-intro',
  templateUrl: './job-eval-intro.component.html',
  styleUrls: ['./job-eval-intro.component.scss']
})
export class JobEvalIntroComponent implements OnInit, OnChanges {
  issuccess: boolean=false;
    @Input() inValue: any = [];
    @Input() isDetailFlag: Boolean;

    workEvaluteResumeList: any[] = [];
    workEvaluteResume: any = {
      subbranchWorkEvaluate: '',
      approveWorkEvaluate: ''
    };        // 工作评价简述

    history: HistoryInfor = new HistoryInfor();

    statusKeeper: JobEvalIntroPageCache = new JobEvalIntroPageCache(this.commfunc);

    markFlag: string = '';
    localData: BranchExamApprovalData = new BranchExamApprovalData(this.commfunc);

    constructor(public commfunc: Commfunc) { }

    ngOnInit() {
      this.getConfidential();
    }

    ngOnChanges(changes: SimpleChanges): void {
      this.grapComponentData();
    }

    // 抓取数据
    grapComponentData () {
      this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
      if (this.inValue && this.inValue.length > 0) {
        this.workEvaluteResumeList = this.inValue;
        let obj = {};
        this.history.historyControl (this.workEvaluteResumeList, obj);
        this.workEvaluteResume = obj;
      }
    }
  

    // 向外部传递数据
    inputCall (event) {
      if(this.workEvaluteResume.subbranchWorkEvaluate&&this.workEvaluteResume.approveWorkEvaluate){
        this.workEvaluteResume['issucess']=true;
        this.issuccess=false;
      } else{
        this.workEvaluteResume['issucess']=false;
        this.issuccess=true;
      }
      this.setWorkEvaluteResume();
    }

    setWorkEvaluteResume () {
        this.statusKeeper.setWorkEvaluteResume(this.workEvaluteResume);
    }

    getConfidential () {
      let confidential = this.localData.getOverdueConfidential();
      if (confidential) {
          this.markFlag = confidential.markFlag;
      }
  }

}
