import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { AllRisksPageCache } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/all-risks/common/pageCache";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";
import { BranchExamApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/base/localData";

@Component({
    selector: 'all-risks',
    templateUrl: './all-risks.component.html',
    styleUrls: ['./all-risks.component.scss']
})
export class AllRisksComponent implements OnInit, OnChanges {

    issuccess: boolean=false;
    @Input() inValue: any = [];
    @Input() isDetailFlag: Boolean;

    catenaMoralRiskList: any[] = [];
    catenaMoralRisk: any = {
        setRisk: '',
        moralRisk: '',
        relePersonRisk: []
    };          // 系列/道德性风险

    msgs: Message[] = [];

    //1.系列性/道德性风险
    seriesRiskOption: any[] = [{ label: '是', value: '1' }, { label: '否', value: '0' }];
    moralRiskOption: any[] = [{ label: '是', value: '1' }, { label: '否', value: '0' }];

    display: Boolean = false;

    history: HistoryInfor = new HistoryInfor();
    statusKeeper: AllRisksPageCache = new AllRisksPageCache(this.commfunc);
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    pageProcess: PageProcess = new PageProcess();


    releCustId: string = '';
    releCustName: string = '';
    customerIndex: number = 0;
    markFlag: string = '';
    localData: BranchExamApprovalData = new BranchExamApprovalData(this.commfunc);



    constructor(
        public httpService: OverdueHttpService,
        public commfunc: Commfunc
    ) { }


    ngOnInit() {
        this.pageProcess.setInit();
        this.getConfidential();
    }


    ngOnChanges(changes: SimpleChanges): void {
        this.grapComponentData();
    }


    // 抓取数据
    grapComponentData() {
        if (this.pageProcess.getInit()) {
            this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
            // 接收父页面传递来的页面
            this.catenaMoralRiskList = this.inValue;
            this.loadData();
        }
    }

    // 加载数据或恢复数据
    loadData() {

        // 有数据
        if (this.inValue && this.inValue.length > 0) {
            this.catenaMoralRiskList = this.inValue;
            let obj = {};
            this.history.historyControl(this.catenaMoralRiskList, obj);
            this.catenaMoralRisk = obj;
        }
        if (!this.catenaMoralRisk || !this.catenaMoralRisk.relePersonRisk || !this.catenaMoralRisk.relePersonRisk.length) {
            if (!this.isDetailFlag) {
                this.addRelative();
            }
        }
    }

    fixNumber(source: any) {
        if (source && source.length) {
            source.forEach(element => {
                for (let property in element) {
                    if (element.hasOwnProperty(property) && typeof element[property] == 'number') {
                        element[property] = parseFloat(element[property]).toFixed(2);
                    }
                }
            })
        }

        return source;
    }


    /***
     * 1.关系化表格的增加和删除列表
     */
    //添加
    addRelative() {
        let arr = [{ releCustId: '', releCustName: '', myBankLoanAmt: '' }]; // releCustName custName
        if (!this.catenaMoralRisk.relePersonRisk) {
            this.catenaMoralRisk.relePersonRisk = [];
        }
        this.catenaMoralRisk.relePersonRisk = this.catenaMoralRisk.relePersonRisk.concat(arr);
        // 保存状态
        this.setCatenaMoralRisk();
    }

    //删除
    deleteRelative(index) {
        if (this.catenaMoralRisk.relePersonRisk.length > 1) {
            //  使用slice等价于将数组进行一次深拷贝，重新分配存储地址
            let arr = this.catenaMoralRisk.relePersonRisk.slice(0, this.catenaMoralRisk.relePersonRisk.length);
            arr.splice(index, 1);
            this.catenaMoralRisk.relePersonRisk = arr;

            // 保存状态
            this.setCatenaMoralRisk();
        } else {
            let arrs = [{ releCustId: '', releCustName: '', myBankLoanAmt: '' }];
            this.catenaMoralRisk.relePersonRisk = this.catenaMoralRisk.relePersonRisk.concat(arrs);
            let arr = this.catenaMoralRisk.relePersonRisk.slice(0, this.catenaMoralRisk.relePersonRisk.length);
            arr.splice(index, 1);
            this.catenaMoralRisk.relePersonRisk = arr;
            this.setCatenaMoralRisk();
            // this.commonlyMessage.logicError('最后一项不可删除')
        }
    }

    // 输入客户号后失焦触发的事件
    cusNumBlur(item, i) {
        this.catenaMoralRisk.relePersonRisk.forEach((element, index) => {
            if (i == index) {
                element.num = item.num;
                element.name = item.num;
                element.money = item.num;
            }
        })
    }

    // 向外部传递数据
    inputCall(event) {
        this.setCatenaMoralRisk();
    }

    dropdownChange(event) {
        this.setCatenaMoralRisk();
    }

    customerPop(index) {
        this.customerIndex = index;
        this.display = true;
    }


    customerCall(event) {
        this.releCustId = event['custNo'];
        this.releCustName = event['custName'];
        this.catenaMoralRisk.relePersonRisk[this.customerIndex].releCustId = this.releCustId;
        this.catenaMoralRisk.relePersonRisk[this.customerIndex].releCustName = this.releCustName;
        this.display = false;

        this.getTableList()
        // this.setCatenaMoralRisk();
    }
    //获取客户欠贷/担保余额查询回显数据
    getTableList() {
        let param = {
            // overdueCaseName: "",         //逾期案例名称
            custNo: this.releCustId      //客户号(必输)
        }
        this.httpService.overdueCustLoanAmtQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let t = data.myBankLoanAmt;
                t = !isNaN(t) ? parseFloat(t).toFixed(2) : '0.00';
                this.catenaMoralRisk.relePersonRisk[this.customerIndex].myBankLoanAmt = t;

                this.setCatenaMoralRisk();
            } else {
                this.commonlyMessage.logicError(data.returnCode.message)
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!')
        });
    }


    setCatenaMoralRisk() {
        if(this.catenaMoralRisk.setRisk&&this.catenaMoralRisk.moralRisk){
            this.catenaMoralRisk['issucess']=true;
            this.issuccess=false;
        }else{
            this.catenaMoralRisk['issucess']=false; 
            this.issuccess=true;
        }
        this.statusKeeper.setCatenaMoralRisk(this.catenaMoralRisk);
    }

    getConfidential() {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }


}
