import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class CreditManEvalPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }


    setDuteousManageEvalute (duteousManageEvalute) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, duteousManageEvalute, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-duteousManageEvalute", JSON.stringify({
            duteousManageEvalute: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getDuteousManageEvalute () {
        return this.getTemporaryData("overdue-duteousManageEvalute");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        // console.log(this.getDuteousManageEvalute())
        let dataTimeStamp = this.getDuteousManageEvalute() ? this.getDuteousManageEvalute().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}