import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { DuteousManageEvaluteHelper } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/helper/innerProblem";
import { CreditManEvalPageCache } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/credit-manage-eval/common/pageCache";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import * as _ from 'lodash';
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { TooltipModule } from "primeng/tooltip";
import { BranchExamApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/base/localData";



@Component({
    selector: 'credit-manage-eval',
    templateUrl: './credit-manage-eval.component.html',
    styleUrls: ['./credit-manage-eval.component.scss']
})
export class CreditManageEvalComponent implements OnInit, OnChanges {
    isShow1: boolean = false;
    selectData: any;
    isShow: boolean = false;
    code: any;//码值
    @Input() inValue: any = [];
    @Input("inValueTwo") overduePersonLiable: any;
    @Input() isDetailFlag: Boolean;

    duteousManageEvaluteList: any[] = [];
    duteousManageEvalute: any = {
        dutyAffirmLevel: '',
        dutyAffirmReason: '',
        innerManageProblems: [],
        overduePersonLiable: []
    };     // 尽职信贷管理评价
    history: HistoryInfor = new HistoryInfor();

    msgs: Message[] = [];

    //4.尽职信贷管理评价

    dutyAffirmLevel: any = [];     // code
    actualData = [];//实际经办责任人
    approvalData = [];//实际审批责任人

    dutyHelper: DuteousManageEvaluteHelper = new DuteousManageEvaluteHelper();
    statusKeeper: CreditManEvalPageCache = new CreditManEvalPageCache(this.commfunc);
    commonlyMessage: CommonlyMessage = new CommonlyMessage();

    // 设置此标志的原因：ngOnChanges 事件第一次执行时，获取不到数据。必须等 ngOnInit 事件执行后才可以获取到。
    // isInit: Boolean = false;

    innerManageProblems: any = [];

    pageProcess: PageProcess = new PageProcess();

    markFlag: string = '';
    localData: BranchExamApprovalData = new BranchExamApprovalData(this.commfunc);

    constructor(public commfunc: Commfunc) {
        this.codeValues();
    }

    ngOnInit() {
        this.pageInit();
        this.getConfidential();
    }

    pageInit() {
        this.dutyHelper.loadBasicOptions(this.code.firstClassProblems, this.code.secondClassProblems);
        this.dutyAffirmLevel = this.code.dutyAffirmLevel;
        this.dutyAffirmLevel.push(
            {label: "请选择", value: ""}
        )
        this.pageProcess.setInit();
    }


    ngOnChanges(changes: SimpleChanges): void {
        this.grapComponentData();
    }


    // 抓取数据
    grapComponentData() {
        if (this.pageProcess.getInit()) {
            this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
            this.duteousManageEvaluteList = this.inValue;


            this.separateData();//分离实际经办责任人和实际审批责任人
            this.loadData();
        }
    }

    loadData() {
        if (this.inValue && this.inValue.length > 0) {
            let obj = {};
            this.history.historyControl(this.duteousManageEvaluteList, obj);
            this.duteousManageEvalute = obj;
            this.provokeDutyChange();
            this.dutyHelper.loadSelectedList(this.duteousManageEvalute['innerManageProblems']);
        } else {
            // 初始化后，没有数据时，1. 恢复页面数据；
            if (this.dutyHelper.firstClass.length && this.dutyHelper.secondClass.length) {
                // 2. 新增一行；
                if (!this.dutyHelper.selectedList ||
                    this.dutyHelper.selectedList.length == 0) {
                    if (!this.isDetailFlag) {
                        this.addQuest();
                    }
                }
            }
        }
    }

    provokeDutyChange() {
        let arr = this.dutyHelper.selectedList;
        if (arr && arr.length > 0) {
            arr.forEach((element, index) => {
                this.dutyChange(null, 1, index);
            });
        }
    }

    //  -----------------------实际经办责任人和实际审批责任人----------------------
    // actualData = [];//实际经办责任人
    // approvalData = [];//实际审批责任人
    // 数据分离
    separateData() {
        let separateData = this.overduePersonLiable;
        this.actualData = separateData.filter(item => {
            return item.liableType == '1';
        })
        this.approvalData = separateData.filter(item => {
            return item.liableType == '2';
        })
        if (this.actualData.length == 0) {
            this.addData();
        }
        if (this.approvalData.length == 0) {
            this.addData1();
        }
    }
    // 实际经办责任人
    receiveData(data) {
        this.actualData = data;
        this.actualData.forEach(item => {
            item.liableType = "1";
        })
        this.duteousManageEvalute.overduePersonLiable = this.actualData.concat(this.actualData);
        this.setDuteousManageEvalute();
    }
    // 实际审批责任人
    receiveDataTwo(data) {
        this.approvalData = data;
        this.approvalData.forEach(item => {
            item.liableType = "2";
        })
        this.duteousManageEvalute.overduePersonLiable = this.actualData.concat(this.approvalData);
        this.setDuteousManageEvalute();

    }
    // 添加数据
    addData() {
        let item = { personLiableName: "", cognizanceRank: "", cognizanceReason: "", liableType: "1" };
        this.actualData.push(item);
    }
    //删除数据
    deleteData(select) {
        let arr = [];
        for (let i = 0; i < this.actualData.length; i++) {
            if (i != select) {
                arr.push(this.actualData[i]);
            }
        }
        this.actualData = arr;

    }
    // 查询责任人
    chooseWorker(index) {
        this.isShow = true;
        this.selectData = index;
    }
    //员工查询返回
    perInfor(item) {
        this.isShow = false;
        if (item.length > 0) {
            this.actualData[this.selectData]["personLiableName"] = item[0].tellerName;
            this.actualData[this.selectData]["personLiableId"] = item[0].tellerId;
        }
    }
    // -----------------------尽职信贷管理评价------------------------
    // 添加
    addQuest() {
        let ret = this.dutyHelper.add();
        if (ret) {
            this.duteousManageEvalute.innerManageProblems = this.dutyHelper.selectedList;
            this.setDuteousManageEvalute();
            this.addFlag();
        } else {
            this.commonlyMessage.logicError('最多只能添加三项');
        }
    }
    //删除
    deleteQuest(index) {
        let ret = this.dutyHelper.delete(index)
        if (ret) {
            this.duteousManageEvalute.innerManageProblems = this.dutyHelper.selectedList;
            this.setDuteousManageEvalute();
            this.deleteFlag(index);
        } else {
            this.dutyHelper.selectedList[0]['firstClass'] = '';
            this.dutyHelper.selectedList[0]['firstClassDesc'] = '';
            this.dutyHelper.selectedList[0]['secondClass'] = '';
            this.dutyHelper.selectedList[0]['secondClassDesc'] = '';
            this.dutyHelper.selectedList[0]['reasonDesc'] = '';
            this.dutyHelper.optionsList[0]['second'] = [];
            this.duteousManageEvalute.innerManageProblems = this.dutyHelper.selectedList;
        }
    }

    dutyChange(event, type, index) {
        this.dutyHelper.selectChange(type, index);
        if (type == 1) {
            this.dutyHelper.firstChange(index);
            if (event) {
                this.clearInnerProblem(index);
            }
        } else {
            this.dutyHelper.selectedList[index]['reasonDesc'] = '';
        }
        this.setDuteousManageEvalute();
    }

    clearInnerProblem(index: number) {
        this.dutyHelper.selectedList[index]['secondClass'] = '';
        this.dutyHelper.selectedList[index]['secondClassDesc'] = '';
        this.dutyHelper.selectedList[index]['reasonDesc'] = '';
    }

    // 向外部传递数据
    inputCall(event) {
        this.setDuteousManageEvalute();
    }
    dropdownChange(event) {
        this.setDuteousManageEvalute();
    }

    setDuteousManageEvalute() {
        this.duteousManageEvalute.overduePersonLiable = this.actualData.concat(this.actualData);
        this.duteousManageEvalute.overduePersonLiable = this.actualData.concat(this.approvalData);
        this.statusKeeper.setDuteousManageEvalute(this.duteousManageEvalute);
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }




    // ----------------------- 提示框 -------------------------

    innerProblemFlag: any = [];
    innerProblemStyle: any = { width: '30%', left: '35%', bottom: '5px' };
    innerProblemDesc: string = '';

    addFlag() {
        this.innerProblemFlag.push(false);
    }

    deleteFlag(index: number) {
        this.innerProblemFlag.splice(index, 1);
    }

    setInnerProblemStyle(index: number, isHistory: boolean) {

        let row = this.dutyHelper.selectedList[index];

        if (isHistory) {
            row = this.history.currentHistory.innerManageProblems[index];
        }

        if (row.secondClass == '0903') {
            this.innerProblemStyle = { width: '30%', left: '53%', bottom: '5px' };
        } else {
            this.innerProblemStyle = { width: '30%', left: '35%', bottom: '5px' };
        }
    }

    problemEnter(type: number, index: number, isHistory: boolean) {

        this.setInnerProblemStyle(index, isHistory);

        let row = this.dutyHelper.selectedList[index];

        if (isHistory) {
            row = this.history.currentHistory.innerManageProblems[index];
        }

        let desc = '';

        switch (type.toString()) {
            case '1':
                desc = row['firstClassDesc'];
                break;
            case '2':
                desc = row['secondClassDesc'];
                break;
            case '3':
                desc = row['reasonDesc'];
                break;
        }

        this.innerProblemDesc = desc;
        if (this.innerProblemDesc) {
            this.innerProblemFlag[index] = true;
        } else {
            this.innerProblemFlag[index] = false;
        }
    }

    problemLeave(type: number, index: number) {
        this.innerProblemFlag[index] = false;
    }


    getConfidential() {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }
    // 添加数据
    addData1() {
        let item = [{ personLiableName: "", cognizanceRank: "", cognizanceReason: "", liableType: "2" }];
        this.approvalData = this.approvalData.concat(item);
    }
    //删除数据
    deleteData1(select) {
        let arr = [];
        for (let i = 0; i < this.approvalData.length; i++) {
            if (i != select) {
                arr.push(this.approvalData[i]);
            }
        }
        this.approvalData = arr;

    }
    // 查询责任人
    chooseWorker1(index) {
        this.isShow1 = true;
        this.selectData = index;
        console.log(this.selectData)
    }
    //员工查询返回
    perInfor1(item) {
        this.isShow1 = false;
        if (item.length > 0) {
            console.log(item);
            console.log(this.selectData);
            this.approvalData[this.selectData]["personLiableName"] = item[0].tellerName;
            this.approvalData[this.selectData]["personLiableId"] = item[0].tellerId;
        }
    }
}
