import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class ComplianceAnalysisPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }


    setComplianceAnalyze (complianceAnalyze) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, complianceAnalyze, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-complianceAnalyze", JSON.stringify({
            complianceAnalyze: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getComplianceAnalyze () {
        return this.getTemporaryData("overdue-complianceAnalyze");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getComplianceAnalyze() ? this.getComplianceAnalyze().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}