import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class ReasonAnalysisPageCache extends PageStatusKeeper {



    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }


    // 逾期原因 
    // 贷款实际用途
    setRealityCreditPurpose (realityCreditPurpose) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, realityCreditPurpose, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-realityCreditPurpose", JSON.stringify({
            realityCreditPurpose: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getRealityCreditPurpose () {
        return this.getTemporaryData("overdue-realityCreditPurpose");
    }

    isRealityCreditPurposeChange (source) {
        let ret = false;
        let current = this.getRealityCreditPurpose();
        if (source && current) {
            for (let prop in current) {
                if (current.hasOwnProperty(prop)) {
                    ret = this.changeMonitor(current[prop], source[prop]);
                    if (ret) {
                        break;
                    }
                }
            }
        }
        return ret;
    }


    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getRealityCreditPurpose() ? this.getRealityCreditPurpose().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }



    // 风险成因
    isDataNeedRollback2 () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getOverdueReasonAnalyze() ? this.getOverdueReasonAnalyze().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }
    setOverdueReasonAnalyze (overdueReasonAnalyze) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, overdueReasonAnalyze, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-overdueReasonAnalyze", JSON.stringify({
            overdueReasonAnalyze: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getOverdueReasonAnalyze () {
        return this.getTemporaryData("overdue-overdueReasonAnalyze");
    }

    
}