import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { CreditDevelopmentPageCache } from "app/pages/tzb/custom/overdue/overdue-case/branch-approval/credit-development/common/pageCache";
import { CreditInfoData } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/base/localData";

@Component({
  selector: 'credit-development',
  templateUrl: './credit-development.component.html',
  styleUrls: ['./credit-development.component.scss']
})
export class CreditDevelopmentComponent implements OnInit, OnChanges {

    issuccess: boolean=false;
    // 输入数据
    @Input() inValue: any = [];
    @Input() isDetailFlag: Boolean;
    workCarryOutConditionList: any[] = [];
    workCarryOutCondition: any = {
        accessOffset: '',
        preLoanInspectionCondition: '',
        examineAndApproveCondition: '',
        afterLoanInspectionCondition: ''
    }

    display: Boolean = false;

    history: HistoryInfor = new HistoryInfor();

    pageCode: string = '4';  // should be 2

    statusKeeper: CreditDevelopmentPageCache = new CreditDevelopmentPageCache(this.commfunc);
    localData: CreditInfoData = new CreditInfoData(this.commfunc);
    markFlag: string = '';

    // 4.授信工作开展情况
    constructor(public commfunc: Commfunc) { 
    }

    ngOnInit() {
        this.getConfidential();
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.grapComponentData();
    }


    // 抓取数据
    grapComponentData () {
        this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
        if (this.inValue && this.inValue.length > 0) {
            this.workCarryOutConditionList = this.inValue;
            let obj = {};

            let l = this.localData.getOperatingLevel();
            // this.history.setOperatingLevel('s');
            this.history.setConfig('2', false, l);
            this.history.historyControl(this.workCarryOutConditionList, obj);
            this.workCarryOutCondition = obj;
        }
    }


    change (event) {
        this.history.historyOptionsChange(event);
    }

    inputCall (event) {
        if(this.workCarryOutCondition.accessOffset&&this.workCarryOutCondition.preLoanInspectionCondition&&this.workCarryOutCondition.afterLoanInspectionCondition&&this.workCarryOutCondition.examineAndApproveCondition){
            this.workCarryOutCondition['issuccess']=true;
            this.issuccess=false;
        }else{
            this.workCarryOutCondition['issuccess']=false;
            this.issuccess=true;
        }
        this.setWorkCarryOutCondition();
    }

    setWorkCarryOutCondition () {
        this.statusKeeper.setWorkCarryOutCondition(this.workCarryOutCondition);
    }

    getConfidential () {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }

}
