import { Router } from "@angular/router/src";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class OverdueCaseTab {

    // tab切换
    show: number = 0;

    // 登陆人员等级
    operatingLevel: number = 0;
    // tabs页路由跳转信息
    tabsRouterInfo: any[] = [];

    isMyOrder: boolean = false;

    constructor (
        public commfunc: Commfunc,
        public router: Router
    ) {}

    showTabs (index, item, param) {
        this.show = index;
        this.router.navigate([item.routerLink, param]);
    }

    //获取tabs页标题信息
    showTabsRouter() {
        this.tabsRouterInfo = [
            { routerLink: '/pages/tzb/custom/overdue/overdue-case/credit-ifo', title: "授信信息", isAllowed: false, index: 0 },
            { routerLink: '/pages/tzb/custom/overdue/overdue-case/overdue-reasons', title: "逾期原因分析", isAllowed: false, index: 1 },
            { routerLink: '/pages/tzb/custom/overdue/overdue-case/topological-graph', title: "拓扑关系", isAllowed: false, index: 2 },
            { routerLink: '/pages/tzb/custom/overdue/overdue-case/branch-approval', title: "支行层级审批意见", isAllowed: false, index: 3 },
            { routerLink: '/pages/tzb/custom/overdue/overdue-case/branch-examination-approval', title: "分行层级审批意见", isAllowed: false, index: 4 },
            { routerLink: '/pages/tzb/custom/overdue/overdue-case/genenal-approval', title: "总行层级审批意见", isAllowed: false, index: 5 },
        ];
    }

    filteTabs () {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let isCustomerMan = commonHeader.extParam.managePermission == 'N';
        let logLevel = 0;
        if (isCustomerMan) {
            logLevel = 2;
        } else {
            logLevel = this.loginLevel(commonHeader.extParam.operatingLevel);
        }
        this.operatingLevel = logLevel;
        this.filter(logLevel);
    }

    filter (level: number) {
        for (let i = 0, len = this.tabsRouterInfo.length; i < len; i++) {
            let element = this.tabsRouterInfo[i];
            if (element.index <= level || this.isMyOrder) {
                element.isAllowed = true;
            } else {
                break;
            }
        }
    }

    loginLevel (type) {
        let ret: number = 0;  // 索引，对应 this.tabsRouterInfo 成员中的index
        switch (type) {
            case 'TEAM':
            case 'DISTRICT':
            case 'SUB_BRANCH':
            case 'DISTRICT_DT':
            case 'SUB_BRANCH_DT':
                ret = 3;
            break;
            case 'BRANCH':
            case 'BRANCH_DT':
                ret = 4;
            break;
            case 'GROUP':
            case 'GENERAL_BANK':
            case 'GENERAL_BANK_DT':
                ret = 5;
            break;
        }
        return ret;
    }


    loadConfig (config) {
        this.isMyOrder = config.isMyOrder;
    }
}