import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class ClearSchemePageCache extends PageStatusKeeper {

    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }

    setClearScheme (clearScheme) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, clearScheme, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-clearScheme", JSON.stringify({
            clearScheme: clearScheme,
            timeStamp: this.getTimeStamp().timeStamp
        }));
      }

    getClearScheme () {
        return this.getTemporaryData("overdue-clearScheme");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getClearScheme() ? this.getClearScheme().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }


}