import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { SelectHelper } from "app/pages/tzb/custom/overdue/common/selectHelper";

// 尽职信贷管理评价 duteousManageEvalute
export class DuteousManageEvaluteHelper {
    
        selectedList: any = [];       // 当前数据

        firstClass: any = [];       // 一级原始数据
        secondClass: any = [];      // 二级原始数据

        optionsList: any[] = [];    // 不同下拉框的数据   [{ first: [], second: [] }, { first: [], second: [] }, { first: [], second: [] }]  长度最多为三
        
        lengthLimit: number = 3;    // 操作限制

        selectHelper: SelectHelper = new SelectHelper();
        objectHelper: ObjectHelper = new ObjectHelper();
    
    
        constructor () {}
    
    
        init () {
            this.add();
        }
    
        add() {
            let ret = false;
            //if (this.selectedList.length < this.lengthLimit) {
                // let item = { firstClass: this.firstClass[0].value, secondClass: this.secondClass[0].value };
                let item = { firstClass: '', secondClass: '' };
                this.addOptionsList();
                this.selectedList.push(item);
                ret = true;
            // } 
            return ret;
        }
    
        //删除
        delete(index) {
            let ret = false;
            if (this.selectedList.length > 1) {
                this.deleteOptionsList(index);
                this.selectedList.splice(index, 1);
                ret = true;
            }
            return ret;
        }
    
    
        selectChange (type: number, index: number) {
            let row = this.selectedList[index];
            if (type == 1) {
                row['firstClassDesc'] = this.selectHelper.getLabel(this.firstClass, row['firstClass']);
            } else {
                row['secondClassDesc'] = this.selectHelper.getLabel(this.secondClass, row['secondClass']);
            }
        }
    
        addOptionsList () {
            let obj = { first: [], second: [] };
            obj.first = this.copyList(this.firstClass);
            obj.second = this.copyList(this.secondClass);
            this.optionsList.push(obj);
        }
    
        deleteOptionsList (index) {
            this.optionsList.splice(index, 1);
        }
    
        firstChange (index) {
            let value = this.selectedList[index]['firstClass'];
            let row = this.optionsList[index];
            row.second = this.selectHelper.itemsfilter(value, this.copyList(this.secondClass));
        }
    
        // 加载初始化选项
        loadBasicOptions (firstClass, secondClass) {
            this.firstClass = firstClass;
            this.secondClass = secondClass;
        }
    
        // 加载已选中的数据
        loadSelectedList (current) {
            if (current) {
                this.selectedList = current;
                this.preLoadOptions();
            }
        }
    
        // 加载选项
        preLoadOptions () {
            this.selectedList.forEach(element => {
                this.addOptionsList();
            })
        }
    
    
        // 列表拷贝
        copyList (source) {
            let ret = [];
    
            source.forEach(element => {
                let obj = {}
                this.objectHelper.copyObject(obj, element);
                ret.push(obj);
            })
    
            return ret;
        }
    
    }
    