import { Component, OnInit, Output, Input, EventEmitter, OnChanges, AfterContentChecked, AfterViewChecked } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { OverdueHttpService } from '../../../http/overdue.http.service';
import { element } from 'protractor';
import { OverdueReasonsBean } from './../bean/overdue-reasons.bean';
import { BondsmanAnalysisPageCache } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/bondsman-analysis/common/pageCache";
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { MyFormValidate } from "app/pages/tzb/custom/overdue/common/formValidate";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";

// 3.担保人情况分析
@Component({
    selector: "bondsman-analysis",
    templateUrl: './bondsman-analysis.component.html',
    styleUrls: ['./bondsman-analysis.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService]
})

export class BondsmanAnalysis implements OnInit, OnChanges, AfterContentChecked {
    @Input() inValue: any;
    @Input() isDetailFlag: Boolean;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    code: any;//码值
    msgs: Message[] = [];
    overdueReasonsBean: OverdueReasonsBean = new OverdueReasonsBean();
    // 担保人情况分析列表
    bondsmanConditionList: any[] = [];
    bondsmanCondition: any = [];
    historyBondsmanConditionList: any[] = [];
    hasHistory: Boolean = false;
    currentHistoryBondsmanCondition: any = []; // 当前历史

    // 下拉框
    insteadPayAbility: any[] = []; //有无代偿能力
    insteadPayWill: any[] = []; //有无代偿意愿
    // 模态框
    display: Boolean = false;
    historyTextInfo: any = [];//历史展示信息
    historyOptions: any = []; //接收历史列表 
    currentLevel: string = '';


    statusKeeper: BondsmanAnalysisPageCache = new BondsmanAnalysisPageCache(this.commfunc);
    toolBag: ObjectHelper = new ObjectHelper();
    formValidate: MyFormValidate = new MyFormValidate();
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    localData: LocalData = new LocalData(this.commfunc);
    markFlag: string = '';



    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
    ) {
        this.codeValues() //调用方法，获取全部码值
        // 下拉框
        this.insteadPayAbility = this.code['insteadPayAbility'];//有无代偿能力
        this.insteadPayWill = this.code['insteadPayWill'];//有无代偿意愿
    }



    ngOnInit() {
        this.getTableList();
        this.getConfidential();
    }


    ngOnChanges() {
        this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
        // 接收父页面传过来的参数
        if (this.inValue.length > 0) {
            this.bondsmanConditionList = this.inValue;
            let obj = [];
            obj = this.historyControl();
            this.bondsmanCondition = obj;
        }
    }

    // 提交数据到父页面(只提交逾期后的数据)
    ngAfterContentChecked() {
        //逾期后的数据
        this.outValue.emit(this.bondsmanCondition);
    }

    //获取客户欠贷/担保余额查询回显数据
    getTableList() {
        this.bondsmanConditionList.forEach((element, index) => {
            let param = {
                // overdueCaseName: "",         //逾期案例名称
                custNo: element.releCustId      //客户号(必输)
            }
            this.httpService.overdueCustLoanAmtQuery(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    element["myBankLoanAmt"] = data.myBankLoanAmt;     //本行欠贷余额
                    element["myBankEnsureAmt"] = data.myBankEnsureAmt;  //本行担保余额
                } else {
                    this.commonlyMessage.logicError(data.returnCode.message)
                }
            }, err => {
                this.commonlyMessage.logicError('调用失败!')
            });
        })
    }

    popupClosed () {
        this.currentLevel = '';
    }

    historyOptionsChange (event) {
        this.display = true;
        this.currentLevel = event.value;
        this.currentHistoryBondsmanCondition = this.findCurrentHistory(event.value);
    }

    findCurrentHistory (historyProcess) {
        let ret = [];
        this.historyBondsmanConditionList.forEach(list => {
            if (list[0].historyProcess == historyProcess) {
                ret = list.slice(0);
            }
        })
        return ret;
    }


    historyControl () {
        let ret = [];
        if (this.bondsmanConditionList) {
            if (this.bondsmanConditionList.length == 1) {
                //this.bondsmanCondition = this.bondsmanConditionList[0];
                ret =  this.bondsmanConditionList[0];
            } else if (this.bondsmanConditionList.length > 1) {
                this.hasHistory = true;
                let array = this.uniqueHistoryProcess();
                this.historyOptions = this.combineHistoryOptions(array);
                ret = this.assignBondsmanCondition();
            }
        }
        return ret;
    }

    assignBondsmanCondition () {
        let ret = [];
        let array = this.sortByDesc(this.uniqueHistoryProcess());
        let currentHistoryProcess = array[0].historyProcess;
        let source = this.bondsmanConditionList;
        source.forEach(list => {
            let historyProcess = list[0].historyProcess;
            if (historyProcess == currentHistoryProcess) {
                //this.bondsmanCondition = list.slice(0);
                ret = list.slice(0);
            } else {
                this.historyBondsmanConditionList.push(list);
            }
        })
        return ret;
    }

    combineHistoryOptions (array) {
        let ret = [];

        for (let i = 1, len = array.length; i < len; i++) {
            let item = array[i];
            let obj = { label: '', value: '' };
            switch (item.historyProcess) {
                case '1':
                    obj.label = '客户经理';
                    obj.value = '1';
                    ret.push(obj);
                break;
                case '2':
                    obj.label = '支行层级';
                    obj.value = '2';
                    ret.push(obj);
                break;
                case '3':
                    obj.label = '分行层级';
                    obj.value = '3';
                    ret.push(obj);
                break;
                case '4':
                    obj.label = '总行层级';
                    obj.value = '4';
                    ret.push(obj);
                break;
            }
        }
        return ret;
    }

    uniqueHistoryProcess () {
        let array = this.bondsmanConditionList;
        let ret = [];
        array.forEach(list => {
            let historyProcess = list[0].historyProcess;
            if (ret.indexOf(historyProcess) == -1) {
                ret.push({historyProcess: historyProcess});
            }
        })
        return ret;
    }

    sortByDesc (list) {
        return list.sort((A, B) => B.historyProcess - A.historyProcess);
    }


    // 将数据传递到外部
    inputCall (event, targets) {
        let ts = targets.split('.');
        this.bondsmanCondition[ts[0]][ts[1]] = this.formValidate.onlyNumber(this.bondsmanCondition[ts[0]][ts[1]]);
        this.setBondsmanCondition();
    }
    bondsmanChange (event) {
        this.setBondsmanCondition();
    }
    setBondsmanCondition () {
        this.statusKeeper.setBondsmanCondition(this.bondsmanCondition);
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }

    getConfidential () {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }

}
