
import { Component, OnInit, OnDestroy, Input, OnChanges } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
import * as _ from 'lodash';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import { HttpService } from 'app/pages/tzb/workflow/activiti-work-flow/common/service/http.service';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { MessageService } from "primeng/components/common/messageservice";


@Component({
  selector: 'approve-history',
  templateUrl: './approve-history.component.html',
  styleUrls: ['./approve-history.component.scss'],
  providers: [ HttpService, MessageService, CustomeHttpService, ProcessInstanceManagementService, Commfunc ]
})

export class ApproveHistoryComponent implements OnInit, OnChanges {
    @Input() config: any;

    //获取所有码值
    code: any;

    tableData: any = [];//接收审批流程查询数据

    // 分页数据
    total: number;
    pageSize: number;
    pageNum: number;
    first: any;

    processId: string = '';

    totalNum: number = 0;
    subprocList: any;
    mainTaskId: string = '';
    subprocDisplay: boolean = false;


    pageProcess: PageProcess = new PageProcess();
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    workFlowResultType: any = [];

    constructor(
        public httpService: HttpService,
        public customService: CustomeHttpService,
        public commfunc: Commfunc,
        private processInstanceManagementService: ProcessInstanceManagementService
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.workFlowResultType = this.code['WorkFlowResultType'];
    }

    ngOnInit() {
        this.pageProcess.setInit();
        this.grapParameters();
    }
    ngOnChanges() {
    }
    ngOnDestroy() {
    }

    grapParameters () {
        if (this.config && Object.keys(this.config).length) {
            this.processId = this.config.processId;

            this.initial();
        }
    }

    initial () {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        if (this.processId) {
            // 查询审批记录
            this.bpmQueryTaskList()
        } else {
            this.tableData = [];
        }
    }

    // 查询(历史)审批记录
    bpmQueryTaskList() {
        let param = {
            processId: this.processId,
            isAllNode: false,
            curPage: 1,
            pageRows: 99,
        }
        this.customService.bpmQueryTaskList(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.tableData = data.pageData.result;
                this.getSubProc();
            } else {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            }
        }, error => {
            this.commonlyMessage.logicError('调用服务失败！');
        })
    }

    getSubProc () {
        for (let i = 0, len = this.tableData.length; i < len; i++) {
            let col = this.tableData[i];
            if (col.taskType == 'subproc') {
                this.viewSubprocInit(col);
            }
        }
    }


    // 查看子流程初始化
    viewSubprocInit (col) {
        this.mainTaskId = col.taskId;
        this.pageSize = 99;
        this.pageNum = 1;
        this.first = 0;
        this.viewSubproc()
    }

    // 查看子流程
    viewSubproc() {
        let paramSub = {
            mainTaskId: this.mainTaskId,
            isAllNode: false,
            pageRows: this.pageSize,
            curPage: this.pageNum,
        }
        this.processInstanceManagementService.queryChildTaskList(paramSub).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let subflowInstance = {};
                subflowInstance = data.pageData[0].result;
                this.subprocList = subflowInstance ? subflowInstance[0].workList : [];
                this.totalNum = this.subprocList.length;
                this.subprocDisplay = true;
            } else {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            }
        }, error => {
            this.commonlyMessage.logicError('调用服务失败！');
        })
    }

    // 关闭子流程审批信息
    closesubproc() {
        this.subprocList = [];
        this.subprocDisplay = false;
    }

    //分页
    paginate(event) {
        this.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        this.viewSubproc();
    }



    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}
