import { Component, OnInit, Output, Input, EventEmitter, OnChanges, SimpleChange } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";//码值

@Component({
  selector: 'component-credit-actual',
  templateUrl: './component-credit-actual.component.html',
  styleUrls: ['./component-credit-actual.component.scss']
})
export class ComponentCreditActual implements OnInit {
  @Output("givData")
  givData = new EventEmitter();
  @Input() inValue: any;
  orgId: any;
  title: any;
  inData = [{ personLiableName: "", cognizanceRank: "", cognizanceReason: "" }];
  isShow = false;
  selectData: any;//选中的数据
  userInValue: any;//查员工的入参
  dutyAffirmLevel: any = [];//等级码值

  constructor(public commfunc: Commfunc) {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (commonHeader.extParam.orgId) {
      this.orgId = commonHeader.extParam.orgId;//获取机构ID
    }
    this.codeValues();
  }

  //码值获取
  codeValues() {
    this.commfunc.codeValue();
    let code = this.commfunc.codeDatas;
    this.dutyAffirmLevel = code.dutyAffirmLevel;//等级码值
  }

  ngOnInit() {
    console.log(this.inValue);
  }
  // ngOnChanges(changes: SimpleChange) {
  //   this.saveData();
  // }
  // 保存数据
  saveData() {
    this.givData.emit(this.inData);
  }
  // 添加数据
  addData() {
    let item = [{ personLiableName: "", cognizanceRank: "", cognizanceReason: "" }];
    this.inData = this.inData.concat(item);
  }
  //删除数据
  deleteData(select) {
    let arr = [];
    for (let i = 0; i < this.inData.length; i++) {
      if (i != select) {
        arr.push(this.inData[i]);
      }
    }
    this.inData = arr;

  }
  // 查询责任人
  chooseWorker(index) {
    this.isShow = true;
    this.selectData = index;
    console.log(this.selectData)
  }
  //员工查询返回
  perInfor(item) {
    this.isShow = false;
    if (item.length > 0) {
      console.log(item);
      console.log(this.selectData);
      this.inData[this.selectData]["personLiableName"] = item[0].tellerName;
      this.inData[this.selectData]["personLiableId"] = item[0].tellerId;
    }
  }

}