import { Component, OnInit, Output, Input, OnChanges, Inject, forwardRef, EventEmitter, DoCheck, AfterContentChecked, AfterViewChecked } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService, Checkbox } from 'primeng/primeng';
import { Router } from '@angular/router';
import { element } from 'protractor';
import { debug } from 'util';
import { OverdueHttpService } from '../../../http/overdue.http.service';
import { CreditIfo } from '../credit-ifo.component';
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { SelfDebtPageCache } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/selfDebt/common/pageCache";
// import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { CreditInfoData } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/base/localData";
import { UrlTool } from "app/pages/tzb/custom/overdue/common/url";
import { TransformationPlanHttpService } from "app/pages/tzb/custom/credit-ccm/transformation-plan/http/transformation-plan.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";


//自身债务
@Component({
    selector: "self-debt",
    templateUrl: 'selfDebt.component.html',
    styleUrls: ['selfDebt.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService]
})
export class SelfDebt implements OnInit, OnChanges, AfterContentChecked {

    code: any;
    guaranteeMethod: any = [];   // 担保方式

    // 基础信息
    @Input() putbaseInfo: any;
    baseInfo: any = {};

    @Input() inValue: any;
    // 当前 转化合理性评判
    transformRationalitList: any[] = [];            // 转化合理性评判列表
    // 当前页面显示的转化合理性评判数据
    transformRationalitCurrent: any = {
        transformRationalit: ''
    };
    selfDebtList: any = [];  //自身债务列表
    selfDebtInfo: any = {};
    transformRationalitFlag: Boolean = false;       // 转化合理性评判显示标致
    hasBondsmanConditionFlag: Boolean = false;      // 担保人情况分析 显示标志

    currencyOptions = [];//币种
    @Input() isDetailFlag: Boolean;

    history: HistoryInfor = new HistoryInfor();
    statusKeeper: SelfDebtPageCache = new SelfDebtPageCache(this.commfunc);
    localData: CreditInfoData = new CreditInfoData(this.commfunc);
    pageProcess: PageProcess = new PageProcess();


    urlParam:string;//路径
    @Output() outValue: EventEmitter<any> = new EventEmitter();

    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    urlTool: UrlTool = new UrlTool(this.router, this.commfunc, this.transformationService, this.commonlyMessage);
    markFlag: string = '';


    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        @Inject(forwardRef(() => CreditIfo)) public app: CreditIfo,
        public transformationService: TransformationPlanHttpService
    ) {
        //调用方法，获取全部码值
        this.codeValues()
        // this.statusId = this.code['useStatus'];
        // this.channelType = this.code['mesgSendChanellSequ1'];
        this.guaranteeMethod = this.code['GuaranteeMethod'];
        this.urlParam = window.location.host;
    }
    // 币种		1		人民币
    // 币种		100		折汇人民币
    // 币种		101		临时折人民币
    // 币种		12		英镑
    // 币种		13		港币
    // 币种		14		美元
    // 币种		27		日元
    // 币种		38		欧元
    // 币种		99		外币折美元
    ngOnInit() {
        this.pageProcess.setInit();
        this.getConfidential();
        // this.codeVal();
        this.currencyOptions = this.code['CURRENCY'];
    }


    ngOnChanges() {
        this.grapParameters();
    }

    grapParameters () {
        if (this.pageProcess.getInit()) {
            //接收父组件传值
            this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;
            this.baseInfo = this.putbaseInfo;
            this.loadData();
        }
    }

    loadData () {
        if (Object.keys(this.inValue).length != 0) {
            this.selfDebtList = this.fixedData(this.inValue.selfDebtList);       // 自身债务列表
            this.transformRationalitList = this.inValue.transformRationalitList; // 转化合理性评判列表

            let l = this.localData.getOperatingLevel();
            this.history.setOperatingLevel(l);

            this.history.historyControl (this.transformRationalitList, this.transformRationalitCurrent);

            // 获取转化合理性评判显示标致
            this.grapTransformRationalitFlag();   
        }
    }


    fixedData (list) {
        list.forEach(element => {
            element.guaranteeTypeDesc = this.getLabel(element.guaranteeType);
            element.transformLoanDesc = element.transformLoan == '1' ? '是' : '否';
            element.togetherBorrowList = this.fixLinkLabel(element.togetherBorrowList, 'releCustName', 'releCustNameDesc');
            element.bondsmanList = this.fixLinkLabel(element.bondsmanList, 'releCustName', 'releCustNameDesc');
            element.transformshemeList = this.fixLinkLabel(element.transformshemeList, 'transformLoanApplyId', 'transformLoanApplyIdDesc');
        });
        return list;
    }
    fixLinkLabel (list, origin, desc) {
        let ret = [];
        if (list && list.length) {
            list.forEach((element, index) => {
                if (index < list.length - 1 && element[origin]) {
                    element[desc] = element[origin] + ','
                }
                ret.push(element);
            })
        }
        return ret;
    }
    getNames (list, nameAttr) {
        let ret = '';

        if (list && list.length) {
            list.forEach(element => {
                ret += element[nameAttr] ? element[nameAttr] + ',' : '';
            })
    
            ret = ret.substring(0, ret.length - 1);
        }

        return ret;
    }
    getLabel (key) {
        let ret = '';
        for (let i = 0, len = this.guaranteeMethod.length; i < len; i++) {
            let element = this.guaranteeMethod[i];
            if (element.value == key) {
                ret = element.label;
            }
        }
        return ret;
    }

    grapTransformRationalitFlag () {
        let trans_bondsman_flag = this.localData.getTemporaryData("overdue-transformRationalit-bondsmanCondition");
        if (trans_bondsman_flag) {
            this.transformRationalitFlag = trans_bondsman_flag.transformRationalitFlag;
            this.hasBondsmanConditionFlag = trans_bondsman_flag.hasBondsmanConditionFlag;
        }
        // 2019-7-18  应罗依妮要求更改转化合理性评判显示规则，改由是否转化来判断显示
        
        // if(this.selfDebtList && this.selfDebtList.length >= 1){
        //     this.selfDebtList.forEach(element => {
        //         if(element.transformLoanDesc == "是"){
        //             this.transformRationalitFlag = true;
        //             }
        //     });
        // }

        // console.log(this.transformRationalitFlag);
    }

    // 转化合理性评判 keyup 事件，for other module
    transformRationalitCall (event) {
        this.setSelfDebtInfo();
    }

    setSelfDebtInfo () {
        this.statusKeeper.setSelfDebtInfo(this.transformRationalitCurrent.transformRationalit);
    }



    // -------------------------------------- 

    getConfidential () {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }

    toLink (transformType: string, dataId: string, contractNumber: string, transformLoanApplyId: string) {
        this.urlTool.transform(transformType, dataId, contractNumber, transformLoanApplyId);
    }

    // 提交数据到父页面
    ngAfterContentChecked() {
        // 转化合理性评判
        let param = this.transformRationalitCurrent.transformRationalit;
        this.outValue.emit(param);
    }
    
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }



}
