import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { OverdueHttpService } from '../../../http/overdue.http.service';
import { element } from 'protractor';
import { debug } from 'util';
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { UrlTool } from "app/pages/tzb/custom/overdue/common/url";
import { TransformationPlanHttpService } from "app/pages/tzb/custom/credit-ccm/transformation-plan/http/transformation-plan.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";

//关联债务
@Component({
    selector: "rele-debt",
    templateUrl: 'releDebt.component.html',
    styleUrls: ['releDebt.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService]
})

export class ReleDebt implements OnInit ,OnChanges{
    @Input() inValue: string;
    msgs: Message[] = [];
    code: any;
    releList: any=[];  //关联债务列表

    // 担保方式
    guaranteeMethod: any = [];

    pageProcess: PageProcess = new PageProcess();
    urlParam:string;//路径
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    urlTool: UrlTool = new UrlTool(this.router, this.commfunc, this.transformationService, this.commonlyMessage);

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public transformationService: TransformationPlanHttpService
    ) {
        //调用方法，获取全部码值
        this.codeValues()
        // this.statusId = this.code['useStatus'];
        // this.channelType = this.code['mesgSendChanellSequ1'];
        this.guaranteeMethod = this.code['GuaranteeMethod'];
        this.urlParam = window.location.host;
    }

    ngOnInit() {
        this.pageProcess.setInit();
    }
    
    ngOnChanges(){
        if (this.pageProcess.getInit()) {
            this.grapParameters();
        }
    }

    grapParameters () {
        // 接收父页面传过来数据
        if (this.inValue && this.inValue.length) {
            // console.log(this.inValue)
            this.releList = this.fixedData(this.inValue);
            // console.log(this.releList)
        } else {
            this.releList = [];
        }
    }

    fixedData (list) {
        list.forEach(element => {
            element.guaranteeTypeDesc = this.getLabel(element.guaranteeType);
            element.transformLoanDesc = element.transformLoan == '1' ? '是' : '否';
            element.togetherBorrowList = this.fixLinkLabel(element.togetherBorrowList, 'releCustName', 'releCustNameDesc');
            element.bondsmanList = this.fixLinkLabel(element.bondsmanList, 'releCustName', 'releCustNameDesc');
            element.transformshemeList = this.fixLinkLabel(element.transformshemeList, 'transformLoanApplyId', 'transformLoanApplyIdDesc');
            element.releRelationTypeNameDesc = this.getNames(element.releList, 'releRelationTypeName')
            element.releTypeNameDesc = this.getNames(element.releList, 'releTypeName')
        });
        return list;
    }
    fixLinkLabel (list, origin, desc) {
        let ret = [];
        if (list && list.length) {
            list.forEach((element, index) => {
                if (index < list.length - 1 && element[origin]) {
                    element[desc] = element[origin] + ','
                }
                ret.push(element);
            })
        }
        return ret;
    }
    getNames (list, nameAttr) {
        let ret = '';

        if (list && list.length) {
            list.forEach(element => {
                ret += element[nameAttr] ? element[nameAttr] + ',' : '';
            })
    
            ret = ret.substring(0, ret.length - 1);
        }

        return ret;
    }
    getLabel (key) {
        let ret = '';
        for (let i = 0, len = this.guaranteeMethod.length; i < len; i++) {
            let element = this.guaranteeMethod[i];
            if (element.value == key) {
                ret = element.label;
            }
        }
        return ret;
    }

    toLink (transformType: string, dataId: string, contractNumber: string, transformLoanApplyId: string) {
        this.urlTool.transform(transformType, dataId, contractNumber, transformLoanApplyId);
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }



}