import { Component, OnInit, Output, Input, EventEmitter, OnChanges, DoCheck, AfterContentChecked, AfterViewChecked } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { OverdueHttpService } from '../../../http/overdue.http.service';
import { element } from 'protractor';
import { OverdueReasonsBean } from './../bean/overdue-reasons.bean';
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { HistoryHelper, RiskReasonHelper } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/reason-analysis/helper/helper";
import { SelectHelper } from "app/pages/tzb/custom/overdue/common/selectHelper";
import { RealityCreditPurposeHelper } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/reason-analysis/helper/realityHelper";
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { ReasonAnalysisPageCache } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/reason-analysis/common/pageCache";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { MyFormValidate } from "app/pages/tzb/custom/overdue/common/formValidate";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";
import { TooltipModule } from "primeng/tooltip";


//2.逾期原因分析
@Component({
    selector: "reason-analysis",
    templateUrl: './reason-analysis.component.html',
    styleUrls: ['./reason-analysis.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService, CusOperationHttpService]
})

export class ReasonAnalysis implements OnInit {
    @Input() inValue: any;
    @Input() realValue: any;
    @Input() originValue: any;
    @Input() isDetailFlag: Boolean;
    @Output() outValue: EventEmitter<any> = new EventEmitter();

    code: any;//码值
    // msgs: Message[] = [];
    // overdueReasonsBean: OverdueReasonsBean = new OverdueReasonsBean();


    // 组件参数 for other module
    // 实际用途
    realityCreditPurposeList: any[] = [];
    realityCreditPurpose: any = {
        firstPurpose: '',
        firstPurposeName: '',
        secondPurpose: '',
        secondPurposeName: '',
        thirdPurpose: '',
        thirdPurposeName: '',
        purposeRemark: ''
    }

    // 逾期原因
    overdueReasonAnalyzeList: any = [];
    // 当前 预期原因分析
    overdueReasonAnalyze: any = {
        originalFinanceDataDesc: '',
        financeChangeReason: '',
        notFinanceChangeReason: '',
        riskReason: []
    };


    originalCreditPurposeString: string = '';

    // 风险成因 相关数据
    riskReasonHelper: RiskReasonHelper = new RiskReasonHelper();
    // 实际贷款用途 联动帮助类
    realityHelper: RealityCreditPurposeHelper = new RealityCreditPurposeHelper();
    // // 历史纪录
    historyHelper: HistoryHelper = new HistoryHelper();
    selectHelper: SelectHelper = new SelectHelper();
    toolBag: ObjectHelper = new ObjectHelper();
    statusKeeper: ReasonAnalysisPageCache = new ReasonAnalysisPageCache(this.commfunc);
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    formValidate: MyFormValidate = new MyFormValidate();

    // 设置此标志的原因：ngOnChanges 事件第一次执行时，获取不到数据。必须等 ngOnInit 事件执行后才可以获取到。
    isInit: Boolean = false;
    managePermission: string = '';
    superviceRiskReasonAnalyze: any = {
        firstClass: '',
        firstClassDesc: ''
    }

    historySuperviceRiskReason: any;

    isCredit: boolean = false;

    localData: LocalData = new LocalData(this.commfunc);
    markFlag: string = '';

    // 提示框相关 
    originalCreditFlag: boolean = false;
    CreditPurposeFlag: boolean = false;



    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public cusOperationHttpService: CusOperationHttpService,
    ) {
        //调用方法，获取全部码值
        this.codeValues()
        // this.insteadPayAbility = this.code['insteadPayAbility'];//有无代偿能力
        this.getProType();
    }

    ngOnInit() {
        // 风险成因
        // this.reasonList = [{ first: this.firstOptions[0].value, second: this.secondOptions[0].value }];
        this.pageInit();
        this.setInit();

        this.getConfidential();
    }

    setInit() {
        this.isInit = true;
    }
    getInit() {
        return this.isInit;
    }

    pageInit() {

        // 获取登录人是否是客户经理（N表示客户经理，Y表示非客户经理），贷前的列表客户经理不可查看
        this.managePermission = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission;

        // 风险成因 加载基础数据
        this.riskReasonHelper.loadBasicReason(this.code.firstClass, this.code.secondClass);

        // 贷款实际用途 加载基础数据
        this.realityHelper.loadOrigins([
            this.code.realityCreditPurpose,
            this.code.realityCreditPurposeLevelTwo,
            this.code.realityCreditPurposeLevelThree
        ]);
    }

    ngOnChanges() {
        this.grapParameters();
    }

    grapParameters() {
        if (this.getInit()) {
            this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;

            // 接收父页面传过来的参数
            this.overdueReasonAnalyzeList = this.inValue;
            this.realityCreditPurposeList = this.realValue;

            this.historyControl();

            if (this.originValue && Object.keys(this.originValue).length > 0) {
                this.combineOriginalCreditPurpose(this.originValue);
            }
        }
    }

    historyControl() {
        // 实际贷款用途
        this.realityCreditPurposeDataCover();
        // 风险成因
        this.overdueReasonAnalyzeDataCover();
        if (this.overdueReasonAnalyzeList.length && this.realityCreditPurposeList.length) {
            this.historyHelper.config();
        }
    }


    // 提交数据到父页面(只提交逾期后的数据)
    ngAfterContentChecked() {
        //逾期后的数据
        this.outValue.emit(this.overdueReasonAnalyze);
    }

    overdueReasonAnalyzeDataCover() {

        if (this.overdueReasonAnalyzeList.length) {
            let obj = {};

            let l = this.localData.getOperatingLevel();
            this.historyHelper.history.setOperatingLevel(l);
            this.historyHelper.history.historyControl(this.overdueReasonAnalyzeList, obj);
            this.overdueReasonAnalyze = obj;
            this.loadHistorySuperviceRiskReason();
            this.loadSuperviceRiskReason();
            this.riskReasonHelper.loadRiskReason(obj["riskReason"]);
            this.setOverdueReasonAnalyze();               // 向外部导出自身债务信息
        } else {
            if (this.riskReasonHelper.firstClass.length &&
                this.riskReasonHelper.secondClass.length) {
                    if (!this.riskReasonHelper.reasonList ||
                        this.riskReasonHelper.reasonList.length == 0) {
                        if (!this.isDetailFlag) {
                            this.addRisk();
                        }
                    }
            }
        }
    }

    loadSuperviceRiskReason() {
        let temp = this.overdueReasonAnalyze.superviceRiskReasonAnalyze;
        this.superviceRiskReasonAnalyze = temp && temp.length ? temp[0] : this.superviceRiskReasonAnalyze;
    }
    loadHistorySuperviceRiskReason() {
        let t = this.historyHelper.history.currentHistory.superviceRiskReasonAnalyze;
        t = t && t.length ? t[0] : null;
        this.historySuperviceRiskReason = t ? t.firstClass : '';
    }

    realityCreditPurposeDataCover() {

        if (this.realityCreditPurposeList && this.realityCreditPurposeList.length) {
            let obj = {};

            let l = this.localData.getOperatingLevel();
            this.historyHelper.history2.setOperatingLevel(l);
            this.historyHelper.history2.historyControl(this.realityCreditPurposeList, obj);
            this.realityCreditPurpose = obj;
            
            this.provokeRealPurposeChange();
            this.setRealityCreditPurpose();               // 向外部导出自身债务信息
        }
    }

    updateRealityCreditPurpose(realityCreditPurpose) {
        // 1. 保存初始化数据
        this.realityCreditPurpose = realityCreditPurpose;
        this.setRealityCreditPurpose();               // 向外部导出自身债务信息
        this.provokeRealPurposeChange();
    }



    // 实际用途  --------------------------数据暂存 和 恢复
    realPurposeChange(event, type) {
        this.clearRealPurpose(type);
        this.realPurposeChangeHandle(event.value, type);
        this.setRealityCreditPurpose();
    }
    clearRealPurpose (type) {
        if (type == 1) {
            this.realityCreditPurpose.secondPurpose = '';
            this.realityCreditPurpose.secondPurposeName = '';
            this.realityCreditPurpose.thirdPurpose = '';
            this.realityCreditPurpose.thirdPurposeName = '';
        } else if (type == 2) {
            this.realityCreditPurpose.thirdPurpose = '';
            this.realityCreditPurpose.thirdPurposeName = '';
        }
    }
    realPurposeChangeHandle(val, type) {
        if (type == 1) {
            this.realityHelper.firstChange(val);
            this.realityCreditPurpose.firstPurposeName = this.selectHelper.getLabel(this.realityHelper.first, val);
        } else if (type == 2) {
            this.realityHelper.secondChange(val);
            this.realityCreditPurpose.secondPurposeName = this.selectHelper.getLabel(this.realityHelper.second, val);
        } else {
            this.realityCreditPurpose.thirdPurposeName = this.selectHelper.getLabel(this.realityHelper.third, val);
        }
    }
    purposeRemarkCall(event) {
        this.setRealityCreditPurpose();
    }
    provokeRealPurposeChange() {
        // 在给下拉框赋值的时候需要更改下拉框的集合
        if (this.realityCreditPurpose) {
            if (this.realityCreditPurpose.firstPurpose) this.realPurposeChangeHandle(this.realityCreditPurpose.firstPurpose, 1);
            if (this.realityCreditPurpose.secondPurpose) this.realPurposeChangeHandle(this.realityCreditPurpose.secondPurpose, 2);
            if (this.realityCreditPurpose.thirdPurpose) this.realPurposeChangeHandle(this.realityCreditPurpose.thirdPurpose, 3);
        }
    }
    setRealityCreditPurpose() {
        this.statusKeeper.setRealityCreditPurpose(this.realityCreditPurpose);
    }


    // -----------------------风险成因------------------------
    // 添加
    addRisk() {
        let ret = this.riskReasonHelper.add();
        if (ret) {
            this.setOverdueReasonAnalyze();
        } else {
            this.commonlyMessage.logicError('最多只能添加三项');
        }
    }

    //删除
    deteleRisk(index) {
        let ret = this.riskReasonHelper.delete(index);
        if (ret) {
            this.setOverdueReasonAnalyze();
        } else {
            this.commonlyMessage.logicError('最后一项不可删除');
        }
    }



    combineOriginalCreditPurpose(obj) {
        this.originalCreditPurposeString = this.finiteStateMachine(obj.firstPurposeName) +
            this.finiteStateMachine(obj.secondPurposeName) +
            this.finiteStateMachine(obj.thirdPurposeName) +
            obj.purposeRemark;
    }

    finiteStateMachine(inputName: string) {
        return inputName ? inputName + ' | ' : '';
    }


    // 风险成因  --------------------------数据暂存 和 恢复
    riskReasonChange(event, type, index) {
        this.riskReasonHelper.selectChange(type, index);
        if (type == 1) {
            this.riskReasonHelper.firstChange(index);
            if (event) {
                this.clearRiskReason(index);
            }
        } else {
            this.riskReasonHelper.reasonList[index]['reasonDesc'] = '';
        }
        this.setOverdueReasonAnalyze();
    }

    clearRiskReason (index) {
        this.riskReasonHelper.reasonList[index]['secondClass'] = '';
        this.riskReasonHelper.reasonList[index]['secondClassDesc'] = '';
        this.riskReasonHelper.reasonList[index]['reasonDesc'] = '';
    }

    inputCall(event) {
        this.setOverdueReasonAnalyze();
    }

    provokeRiskReasonChange() {
        let arr = this.overdueReasonAnalyze.riskReason;
        if (arr && arr.length > 0) {
            arr.forEach((element, index) => {
                this.riskReasonChange(null, 1, index);
                this.riskReasonChange(null, 2, index);
            });
        }
    }

    superViceCall() {
        this.setOverdueReasonAnalyze();
    }

    setOverdueReasonAnalyze() {
        this.overdueReasonAnalyze.superviceRiskReasonAnalyze = [this.superviceRiskReasonAnalyze];
        this.overdueReasonAnalyze.riskReason = this.riskReasonHelper.reasonList
        this.statusKeeper.setOverdueReasonAnalyze(this.overdueReasonAnalyze);
    }


    // ---------------------------------------------


    // 查询产品类型
    getProType() {
        // debugger;
        let baseInfo = this.localData.getTemporaryData('overdue-baseInfo')
        if (!baseInfo.productNo) {
            return;
        }
        let paramproType = {
            productNo: baseInfo.productNo,
            // productNo: '1',
        }
        this.cusOperationHttpService.queryCustProductType(paramproType).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.isCredit = data.productType == 'credit'
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
            this.setConfigs();
        }, error => {
            this.commonlyMessage.logicError('调用服务失败！');
            this.setConfigs();
        })
    }

    setConfigs() {
        // overdue-transformRationalit-bondsmanCondition
        let flags = this.localData.getTemporaryData('overdue-transformRationalit-bondsmanCondition');
        flags['isCredit'] = this.isCredit;
        this.localData.setTemporaryData('overdue-transformRationalit-bondsmanCondition', flags);
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }

    getConfidential() {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }



    // ------------------------------提示框相关
    //realPurposeTip: SelfToolTip = new SelfToolTip();
    realPurposeFlag: boolean = false;
    realPurposeStyle: any = { width: '50%', left: '67%' };
    realPurposeDesc: string = '';
    
    setRealPurposeStyle(isHistory: boolean) {
        let positionFlag = false;
        if (isHistory) {
            if (this.historyHelper.history2.currentHistory.thirdPurpose) {
                positionFlag = true;
            }
        } else {
            if (this.realityHelper.third && this.realityHelper.third.length) {
                positionFlag = true;
            }
        }
        if (positionFlag) {
            this.realPurposeStyle = { width: '50%', left: '100%' };
        } else {
            this.realPurposeStyle = { width: '50%', left: '67%' };
        }
    }

    dropEnter(index: number, isHistory: boolean) {
        
        this.setRealPurposeStyle(isHistory);

        let row = this.realityCreditPurpose;

        if (isHistory) {
            row = this.historyHelper.history2.currentHistory;
        }

        let desc = '';

        switch (index.toString()) {
            case '0':
                desc = row.firstPurposeName;
            break;
            case '1':
                desc = row.secondPurposeName;
            break;
            case '2':
                desc = row.thirdPurposeName;
            break;
        }

        this.realPurposeDesc = desc;
        if (this.realPurposeDesc) {
            this.realPurposeFlag = true;
        } else {
            this.realPurposeFlag = false;
        }
    }

    dropLeave(index: number) {
        this.realPurposeFlag = false;
    }

    // ------------------------------------------------

    riskReasonFlag: any = [
        false, false, false
    ];
    riskReasonStyle: any = { width: '50%', left: '71%', bottom: '5px' };
    riskReasonDesc: string = '';

    setRiskReasonStyle(index: number, isHistory: boolean) {
        let row = this.riskReasonHelper.reasonList[index];
        if (isHistory) {
            row = this.historyHelper.history.currentHistory.riskReason[index];
        }
        if (row.reasonDesc) {
            this.riskReasonStyle = { width: '50%', left: '95%', bottom: '5px' };
        } else {
            this.riskReasonStyle = { width: '50%', left: '71%', bottom: '5px' };
        }
    }

    reasonEnter(type: number, index: number, isHistory: boolean) {

        this.setRiskReasonStyle(index, isHistory);

        let row = this.riskReasonHelper.reasonList[index];

        if (isHistory) {
            row = this.historyHelper.history.currentHistory.riskReason[index];
        }

        let desc = '';

        switch (type.toString()) {
            case '1':
                desc = row['firstClassDesc'];
            break;
            case '2':
                desc = row['secondClassDesc'];
            break;
            case '3':
                desc = row['reasonDesc'];
            break;
        }

        this.riskReasonDesc = desc;
        if (this.riskReasonDesc) {
            this.riskReasonFlag[index] = true;
        } else {
            this.riskReasonFlag[index] = false;
        }
    }

    reasonLeave(type: number, index: number) {
        this.riskReasonFlag[index] = false;
    }

}