import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { SelectHelper } from "app/pages/tzb/custom/overdue/common/selectHelper";



// 历史纪录帮助
export class HistoryHelper {

    // 历史纪录
    display: Boolean = false;
    history: HistoryInfor = new HistoryInfor();   // 
    history2: HistoryInfor = new HistoryInfor();  // 
    hasHistory: Boolean = false;
    historyOptions: any[] = [];
    toolBag: ObjectHelper = new ObjectHelper();
    currentLevel: string = '';


    constructor () {

    }

    config () {
        this.hasHistory = this.history.hasHistory || this.history2.hasHistory;
        this.historyOptions = this.uniqueHistoryOptions();
    }

    uniqueHistoryOptions () {
        let ret = [];
        this.history.historyOptions.forEach(item => {
            if (!this.IsHistoryProcessExist(ret, item.value)) {
                ret.push(item);
            }
        })
        this.history2.historyOptions.forEach(item => {
            if (!this.IsHistoryProcessExist(ret, item.value)) {
                ret.push(item);
            }
        })
        return ret;
    }

    IsHistoryProcessExist (array, historyProcess) {
        let ret = false;
        array.forEach(item => {
            if (item.value == historyProcess) {
                ret = true;
            }
        })
        return ret;
    }

    historyOptionsChange (event) {
        
        this.history.historyOptionsChange(event);

        this.history2.historyOptionsChange(event);

        this.display = true;
    }

    popupClosed () {
        this.currentLevel = '';
    }

}


// 风险成因帮助
export class RiskReasonHelper {

    reasonList: any = [];       // 当前数据
    firstClass: any = [];       // 风险成因 一级原始数据
    secondClass: any = [];      // 风险成因 二级原始数据
    optionsList: any[] = [];    // 风险成因 动态关联 不同下拉框的数据   [{ first: [], second: [] }, { first: [], second: [] }, { first: [], second: [] }]  长度最多为三
    lengthLimit: number = 3;    // 操作限制
    selectHelper: SelectHelper = new SelectHelper();
    objectHelper: ObjectHelper = new ObjectHelper();
    descHolder: any = [false, false, false];


    constructor () {}


    init () {
        this.add();
    }

    add() {
        let ret = false;
        if (!this.reasonList) {
            this.reasonList = [];
        }
        if (this.reasonList.length < this.lengthLimit) {
            this.addOptionsList();
            // let item = { firstClass: this.firstClass[0].value, secondClass: this.secondClass[0].value };
            let item = { firstClass: '', secondClass: '', firstClassDesc: '', secondClassDesc: '' };
            this.reasonList.push(item);
            ret = true;
        } 
        return ret;
    }

    //删除
    delete(index) {
        let ret = false;
        if (this.reasonList.length > 1) {
            this.deleteOptionsList(index);
            this.reasonList.splice(index, 1);
            ret = true;
        }
        return ret;
    }


    selectChange (type: number, index: number) {
        let row = this.reasonList[index];
        if (type == 1) {
            
            row['firstClassDesc'] = this.selectHelper.getLabel(this.firstClass, row['firstClass']);
            
            this.descHolder[index] = false;
        } else {
            let desc = this.selectHelper.getLabel(this.secondClass, row['secondClass']);
            this.setDescHolder(index, desc, true);
            row['secondClassDesc'] = desc;
        }
    }

    setDescHolder (index, desc, isChange) {
        if (isChange) {
            if (desc && desc.indexOf('其他') > -1) {
                this.descHolder[index] = true;
            } else {
                this.descHolder[index] = false;
            }
        } else {
            if (desc) {
                this.descHolder[index] = true;
            } else {
                this.descHolder[index] = false;
            }
        }
    }

    addOptionsList () {
        let obj = { first: [], second: [] };
        obj.first = this.copyList(this.firstClass);
        obj.second = this.copyList(this.secondClass);
        this.optionsList.push(obj);
    }

    deleteOptionsList (index) {
        this.optionsList.splice(index, 1);
    }

    firstChange (index) {
        let value = this.reasonList[index]['firstClass'];
        let row = this.optionsList[index];
        row.second = this.selectHelper.itemsfilter(value, this.copyList(this.secondClass));
    }

    // 加载初始化选项
    loadBasicReason (firstClass, secondClass) {
        this.firstClass = firstClass;
        this.secondClass = secondClass;
    }

    // 加载已选中的风险成因
    loadRiskReason (riskReason) {
        this.reasonList = riskReason;
        if (this.reasonList && this.reasonList.length) {
            this.setAllDescHolder();
            this.preLoadOptions();
        } else {
            this.add();
        }
    }

    setAllDescHolder () {
        this.reasonList.forEach((element, index) => {
            let desc = element.secondClassDesc;
            this.setDescHolder(index, desc, false);
        });
    }

    // 加载选项
    preLoadOptions () {
        this.reasonList.forEach(element => {
            this.addOptionsList();
        })
    }


    // 列表拷贝
    copyList (source) {
        let ret = [];

        source.forEach(element => {
            let obj = {}
            this.objectHelper.copyObject(obj, element);
            ret.push(obj);
        })

        return ret;
    }

}


    