import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { DateHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { UrlTool } from "app/pages/tzb/custom/overdue/common/url";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { SelectHelper } from "app/pages/tzb/custom/overdue/common/selectHelper";
import { TransformationPlanHttpService } from "app/pages/tzb/custom/credit-ccm/transformation-plan/http/transformation-plan.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { Router } from "@angular/router";

@Component({
  selector: 'yearly-check',
  templateUrl: './yearly-check.component.html',
  styleUrls: ['./yearly-check.component.scss']
})
export class YearlyCheckComponent implements OnInit, OnChanges {
  code: any;
  // 输入数据
  @Input() inValue: any;
  @Input() ParentBusinessType: string;
  componentData: any = [];

  pageProcess: PageProcess = new PageProcess();
  dateHelper: DateHelper = new DateHelper();
  commonlyMessage: CommonlyMessage = new CommonlyMessage();
  urlTool: UrlTool = new UrlTool(this.router, this.commfunc, this.httpService, this.commonlyMessage);
  selectHelper: SelectHelper = new SelectHelper();

  // 2.年检
  constructor(
    public router: Router,
    public commfunc: Commfunc,
    public httpService: TransformationPlanHttpService
  ) { 
    this.codeValues();
  }


  ngOnInit() {
    this.pageProcess.setInit();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.pageProcess.getInit()) {
      this.grapComponentData();
    }
  }

  // 抓取数据
  grapComponentData () {
    if (this.inValue && Object.keys(this.inValue).length != 0) {
      // console.log(this.inValue);
      this.componentData = this.fixData(this.inValue);
    } else {
      this.componentData = [];
    }
  }

  fixData (list) {
  
    list.forEach(element => {
      element.sysPushDateDesc = this.dateHelper.getDate(element.sysPushDate);
      element.inspectrecordDesc = this.fixInspectRecord(element.inspectrecord);
      element.inspectOptionDesc = this.fixInspectOption(element.inspectOption);
      element.inspectAnalyzeWayDesc = this.selectHelper.getLabel(this.code['yearly_inspect_analyze_way'], element.inspectAnalyzeWay);
    })

    return list;
  }


  fixInspectRecord (list) {
    return this.combineString(list);
  }

  fixInspectOption (list) {
    return this.combineString(list);
  }


  combineString (list) {
    let ret = '';
    if (list && list.length) {
      if (list.length > 1) {
        list.forEach(element => {
          ret += element.firstClassDesc + ', ' + element.secondClassDesc + '|';
        })
        ret = ret.substring(0, ret.length - 1);
      } else {
        ret += list[0].firstClassDesc + ', ' + list[0].secondClassDesc;
      }
    }
    return ret;
  }

  toLink (dedId: string) {
    this.urlTool.annualInspection(this.ParentBusinessType, dedId);
  }


  //码值
  codeValues() {
      this.commfunc.codeValue();
      let ret = this.commfunc.codeDatas;
      return this.code = ret //code是自己声明的变量
  }


}
