import { Component, OnInit, Input } from '@angular/core';;
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from '../../common/service/commonFun';
import { GraphBean } from './graph.bean';
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';

@Component({
	selector: 'graph-component',
	templateUrl: './graph.component.html',
	styleUrls: ['./graph.component.scss'],
	providers: [CustomeHttpService, Commfunc, ConfirmationService],
}, )
export class GraphComponent implements OnInit {
	@Input() custNo: string;
	@Input() custName: string;

	options: any;
	myChart: any;
	categories: any = [{ name: '人物' }];
	data: any = [];
	links: any = [];
	graphBean: GraphBean = new GraphBean()
	tableData: any = [];
	pageSize: any = 10;
	custList: any;//拓扑图人物集合
	//总页数
	total: string;
	first: number = 0;
	msgs: Message[] = [];
	constructor(
		public httpService: CustomeHttpService,
		public commfunc: Commfunc
	) {


	}

	ngOnInit() {
		this.graphBean.pageNum = 1;
		this.graphBean.pageSize = 10;
		this.graphBean.custNo = this.custNo;
		this.selectCustRelationShipList(this.graphBean)
		this.selectCustRelationShipListByCust({ custNo: this.custNo })
	}

	// 查询表格数据
	selectCustRelationShipList(param) {
		this.httpService.selectCustRelationShipList(param).subscribe(data => {
			if (data.returnCode.code = 'success') {
				this.tableData = data.infoList;
				this.total = data.totalNum
			}
		})
	}
	paginate(event) {
		this.graphBean.pageSize = event.rows * 1;
		//当前页
		let currentPage = event.page + 1;
		this.graphBean.pageNum = currentPage;
		this.first = event.page * event.rows;
		this.selectCustRelationShipList(this.graphBean);
	}
	//查询拓扑图数据
	selectCustRelationShipListByCust(param) {
		this.httpService.selectCustRelationShipListByCust(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.custList = data.infoList;
				if (data.infoList && data.infoList.length > 0) {
					for (let item of data.infoList) {
						if (this.links.length > 0) {
							let curveness = 0.2;
							for (let i of this.links) {
								if (i.source == item.custName && i.target == item.relationCustName) {
									curveness += 0.1;
								}
							}
							let links = {
								source: item.custName,
								target: item.relationCustName,
								name: item.relationName,
								lineStyle: {
									normal: {
										curveness: 0.2,
										width: 1,
										fontSize: 6,
										color: '#000',

									}
								},
								label: {
									show: true,
									fontSize: 10,
									formatter: function (item) {
										if (item.data.name) {
											return item.data.name.indexOf(',') != -1 ? item.data.name.replace(',', ' \n ') : item.data.name;
										} else {
											return ''
										}
									}
								}
							}
							this.links.push(links)
						} else {
							let links = {
								source: item.custName,
								target: item.relationCustName,
								name: item.relationName,
								lineStyle: {
									normal: {
										curveness: 0.2,
										width: 1
									}
								},
								label: {
									show: true,
									fontSize: 8,
									formatter: function (item) {
										debugger
										if (item.data.name) {
											return item.data.name.indexOf(',') != -1 ? item.data.name.replace(',', '\n ') : item.data.name;
										} else {
											return ''
										}
									}
								}
							}
							this.links.push(links)
						}

					}
					console.log(this.links)
					console.log(this.categories)
					let arr = [];
					for (let item of data.infoList) {
						debugger
						let nodes = {}
						if (item.relationCustNo == this.custNo) {
							nodes = {
								name: item.relationCustName,
								category: '本人'
							}
						}
						if (item.partyTypeId == '1' && item.relationCustNo != this.custNo) {
							nodes = {
								name: item.relationCustName,
								category: '个人'
							}
						}
						if (item.partyTypeId == '2' && item.relationCustNo != this.custNo) {
							nodes = {
								name: item.relationCustName,
								category: '企业'
							}
						}
						arr.push(nodes)
						// this.data.push(nodes)
					}

					console.log(this.data)
					for (let i = 0; i < arr.length; i++) {
						let flag = true;
						for (let j = 0; j < this.data.length; j++) {
							if (this.data[j].name == arr[i]['name']) {
								flag = false;
							}
						}
						if (flag) {
							this.data.push(arr[i])
						}
					}


				}
			}

			//渲染拓扑图
			this.options = {
				title: {
					x: 'right',
					y: 'bottom'
				},
				grid: {
					x: 10,
					y: 10
				},
				tooltip: {
					trigger: 'item',
					formatter: '{a}:{b}'
				},
				toolbox: {
					show: true,
					feature: {
						restore: { show: true },
						magicType: { show: true },
						saveAsImage: { show: true }
					}
				},
				legend: {
					x: 'left'
				},
				series: [
					{
						type: 'graph',
						layout: 'force',
						name: "人物关系",
						ribbonType: false,
						label: {
							normal: {
								color: '#fff'
							},
							position: ['50%', '50%']
						},
						force: {
							repulsion: 3000,
							edgeLength: 70
						},
						categories: [
							{
								name: '本人',
								itemStyle: {
									color: '#ffed00'
								}
							},
							{
								name: '个人',
								itemStyle: {

									color: '#03e7d8'
								}
							},
							{
								name: '企业',
								itemStyle: {

									color: '#0eb5f7'
								}
							}
						],

						itemStyle: {
							normal: {
								label: {
									show: true,
									textStyle: {
										color: '#000',//节点文字颜色
										fontSize: 4,
										width: 5
									}
								},
								nodeStyle: {
								},
								lineStyle: {
								}
							},
							rich: {
								a: {
									width: 10
								}

							},
							width: 10

						},
						draggable: true,//是否可以拖拽
						edgeSymbol: ['none', 'arrow'],
						roam: true,//是否可以缩放
						height: 5000,
						nodeScaleRatio: 0,
						symbolSize: 50,//设置节点大小
						focusNodeAdjacency: true,//是否凸显节点及周边节点
						tooltip: {
							formatter: function (item) {
								return item.data.name
							}
						},
						lineStyle: {
							normal: {
								// opacity: 0.9,
								width: 1,
								// curveness: 0.05
							}
						},
						data: this.data,
						links: this.links
					}
				]
			};
		})
	}
	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
	// 360视图跳转
	echartsOn(event) {
		this.custList.forEach(element => {
			if (element.relationCustName == event.name) {
				window.open(`/#/pages/custom-view?custNo=${element.relationCustNo}&pageNum=1`);
			}
		});
	}

	compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}
	// 导出
	openHeaderClick() {
		let param = {
			custNo: this.custNo,
			custName: this.custName,
		}
		this.httpService.custRelationShipListDownLoad(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let fileName = data.fileName;
				let filePath = data.fileUrl;
				open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(fileName) + "&fileUrl=" + filePath)
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
		})
	}

}
