import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class BondsmanAnalysisPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }


    // 逾期原因 
    // 贷款实际用途
    setBondsmanCondition (bondsmanCondition) {
        let list = [];
        this.toolBag.copyObjectListWithSkipping (list, bondsmanCondition, 'historyProcess')
        this.commfunc.setSessionDataCH("overdue-bondsmanCondition", JSON.stringify({
            bondsmanCondition: list,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getBondsmanCondition () {
        return this.getTemporaryData("overdue-bondsmanCondition");
    }

    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getBondsmanCondition() ? this.getBondsmanCondition().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}