import { Component, OnInit, Output, Input, OnChanges, DoCheck, AfterContentChecked, AfterViewChecked, EventEmitter } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { OverdueHttpService } from '../../../http/overdue.http.service';
import { element } from 'protractor';
import { OverdueReasonsBean } from './../bean/overdue-reasons.bean';
import { HistoryInfor } from "app/pages/tzb/custom/overdue/common/history";
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { LoanTablePageCache } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/loantable-analysis/common/pageCache";
import { KeyFilterModule } from "primeng/keyfilter";
import { MyFormValidate } from "app/pages/tzb/custom/overdue/common/formValidate";
import { PageProcess } from "app/pages/tzb/custom/overdue/common/pageProcess";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";


//1.贷款逾期前后列表
@Component({
    selector: "loantable-analysis",
    templateUrl: './loantable-analysis.component.html',
    styleUrls: ['./loantable-analysis.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService]
})

export class LoantableAnalysis implements OnInit, OnChanges, AfterContentChecked {
    @Input() preLoanInValue: any;  //贷前列表
    @Input() overdueInValue: any;  //逾期后列表
    @Input() isDetailFlag: Boolean;
    @Output() outValue: EventEmitter<any> = new EventEmitter();

    code: any;//码值

    overdueReasonsBean: OverdueReasonsBean = new OverdueReasonsBean();

    //Y表示非客户经理，N表示客户经理
    managePermission: string = '';

    //接收父页面传过来的参数
    // isDetailFlagInValue: Boolean; //是否查看历史

    //贷前调查时(回显不可修改)
    preLoanFinancialCondition: any = {
        bankBorrowerAmt: 0,
        societyRaisingAmt: 0,
        accountsPayable: 0,
        debtsTotal: 0,
        receivables: 0,
        stocks: 0,
        fixedAssets: 0,
        investmentsAbroad: 0,
        propertyTotal: 0,
        debts1: 0,
        property1: 0,
        property2: 0,
        rightAndInterests: 0
    };
    //贷款逾期后
    overdueFinancialConditionList: any[] = [];
    overdueFinancialCondition: any = {
        bankBorrowerAmt: 0,
        societyRaisingAmt: 0,
        accountsPayable: 0,
        debtsTotal: 0,
        receivables: 0,
        stocks: 0,
        fixedAssets: 0,
        investmentsAbroad: 0,
        propertyTotal: 0,
        debts1: 0,
        property1: 0,
        property2: 0,
        rightAndInterests: 0
    };

    // 模态框
    history: HistoryInfor = new HistoryInfor();
    toolBag: ObjectHelper = new ObjectHelper();
    statusKeeper: LoanTablePageCache = new LoanTablePageCache(this.commfunc);
    formValidate: MyFormValidate = new MyFormValidate();
    pageProcess: PageProcess = new PageProcess();
    localData: LocalData = new LocalData(this.commfunc);
    markFlag: string = '';


    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
    ) {
        this.codeValues() //调用方法，获取全部码值
        // this.statusId = this.code['useStatus'];
    }

    ngOnInit() {
        this.pageProcess.setInit();
        // 获取登录人是否是客户经理（N表示客户经理，Y表示非客户经理），贷前的列表客户经理不可查看
        this.managePermission = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission;
        this.getConfidential();
    }

    ngOnChanges() {
        this.grapParameters();
    }


    grapParameters () {
        // debugger;
        if (this.pageProcess.getInit()) {
            //接收父组件传值
            this.isDetailFlag = this.isDetailFlag.toString() == "true" ? true : false;

            // 贷前数据firstPurpose
            if (this.managePermission == 'Y' && this.preLoanInValue && Object.keys(this.preLoanInValue).length) {
                this.preLoanFinancialCondition = this.preLoanInValue;
            }

            // 接收父页面传递来的页面
            this.overdueFinancialConditionList = this.overdueInValue;

            this.loadData();
        }
    }

    loadData () {

        // 逾期后数据
        if (this.overdueInValue.length != 0) {
            let obj = {};
            let l = this.localData.getOperatingLevel();
            this.history.setOperatingLevel(l);
            this.history.historyControl (this.overdueFinancialConditionList, obj);
            console.log(this.overdueFinancialConditionList)
            this.overdueFinancialCondition = obj;
        }
    }


    // 提交数据到父页面(只提交逾期后的数据)
    ngAfterContentChecked() {
        //逾期后的数据
        this.outValue.emit(this.overdueFinancialCondition);
    }


    change (event) {
        this.history.historyOptionsChange(event);
    }



    // ------------------------------------数据暂存 和 恢复

    // 传递数据到外部
    inputCall (event, targets, type) {
        let ts = targets.split('.');
        this[ts[0]][ts[1]] = this.formValidate.onlyNumber(this[ts[0]][ts[1]]);
        if (ts[1] != 'debtsTotal' && ts[1] != 'propertyTotal') {
            this.account(ts[1], type);
        }
        this.setOverdueFinancialCondition();
    }

    getTotal (items) {
        let ret = 0;

        for (let i = 0, len = items.length; i < len; i++) {
            let item = items[i];
            let t = this.overdueFinancialCondition[item];
            t = isNaN(t) ? 0 : parseFloat(t);
            ret += t;
        }

        return ret;
    }

    account (target, type) {
        if (type == 1) {
            let items = ['bankBorrowerAmt', 'societyRaisingAmt', 'accountsPayable', 'debts1'];

            this.overdueFinancialCondition.debtsTotal = this.getTotal(items).toFixed(2);
        } else {
            let items = ['receivables', 'stocks', 'fixedAssets', 'investmentsAbroad', 'property1', 'property2'];

            this.overdueFinancialCondition.propertyTotal = this.getTotal(items).toFixed(2);
        }
        // rightAndInterests
        this.overdueFinancialCondition.rightAndInterests = this.overdueFinancialCondition.propertyTotal - this.overdueFinancialCondition.debtsTotal;
        this.overdueFinancialCondition.rightAndInterests = this.overdueFinancialCondition.rightAndInterests.toFixed(2)
    }

    setOverdueFinancialCondition () {
        this.statusKeeper.setOverdueFinancialCondition(this.overdueFinancialCondition);
    }


    // -------------------------------------

    getOperatingLevel () {
        let ret = '';
        let t = this.localData.getOverdueConfidential();
        if (t) {
            ret = t.operatingLevel;
        }
        return ret;
    }


    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }

    getConfidential () {
        let confidential = this.localData.getOverdueConfidential();
        if (confidential) {
            this.markFlag = confidential.markFlag;
        }
    }

}
