import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';

import { Commfunc } from "app/@uisftech/common/service/commonFun";//码值
@Component({
  selector: 'popup-credit-person',
  templateUrl: './popup-credit-person.component.html',
  styleUrls: ['./popup-credit-person.component.scss']
})
export class PopupCreditPerson implements OnInit {
  @Output("givData")
  givData = new EventEmitter();
  @Input("inValue")
  inData: any;

  dutyAffirmLevel: any = [];//等级码值
  constructor(public commfunc: Commfunc) {
    this.codeValues();
  }

  ngOnInit() {
    this.inData = [
      { handlePsersonName: "", cognizanceRank: "", cognizanceReason: "" }
    ]
    // this.getData()
    this.givData.emit()
  }
  // 保存数据
  saveData() {
    console.log(this.inData);
  }
  // 添加数据
  addData() {
    let item = { handlePsersonName: "", cognizanceRank: "", cognizanceReason: "" };
    this.inData.push(item)
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let code = this.commfunc.codeDatas;
    this.dutyAffirmLevel = code.dutyAffirmLevel;//等级码值
  }
  // getData() {
  // }
}
