import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class SelfDebtPageCache extends PageStatusKeeper {

    

    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }

    // 逾期信息
    setSelfDebtInfo (transformRationalit) {
        this.commfunc.setSessionDataCH("overdue-selfDebtInfo", JSON.stringify({
            transformRationalit: transformRationalit,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }
    getSelfDebtInfo () {
        return this.getTemporaryData("overdue-selfDebtInfo");
    }
    isDataNeedRollback () {
        let ret = false;
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getSelfDebtInfo() ? this.getSelfDebtInfo().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }
}