
import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { OverdueCase } from './overdue-case.component';
import { CreditIfo } from './credit-ifo/credit-ifo.component';// 1.逾期案例分析
import { OverdueReasons } from './overdue-reasons/overdue-reasons.component';//2.逾期原因分析
import { TopologicalGraph } from './topological-graph/topological-graph.component';//3.拓扑关系图
import { BranchApproval } from './branch-approval/branch-approval.component';//4.支行层级审批意见
import { BranchExaminationApproval } from './branch-examination-approval/branch-examination-approval.component';//5.分行层级审批意见
import { GenenalApproval } from './genenal-approval/genenal-approval.component';//6.总行层级审批意见
import { CreditApproveHistory } from './credit-approve-history/credit-approve-history.component';
import { CreditApproveOpinion} from './credit-approve-opinion/credit-approve-opinion.component';
const routes: Routes = [
    {
        path: '',
        component: OverdueCase,
        children: [
            // {path: '', redirectTo: 'credit-ifo', pathMatch: 'full' },
            {path:'credit-ifo',component:CreditIfo},
            {path:'overdue-reasons',component:OverdueReasons},
            {path:'topological-graph',component:TopologicalGraph},
            {path:'branch-approval',component:BranchApproval},
            {path:'history',component:CreditApproveHistory},
            {path:'approve',component:CreditApproveOpinion},
            {path:'branch-examination-approval',component:BranchExaminationApproval},
            {path:'genenal-approval',component:GenenalApproval},
        ]
    }
];
export const OverdueCaseRouting: ModuleWithProviders = RouterModule.forChild(routes);
